<template>
  <div v-if="show" data-cy="explicit-eula" class="fixed bg-black bg-opacity-20 flex justify-center items-center top-0 bottom-0 left-0 right-0">
    <div class="max-w-md bg-white rounded-xl shadow-xl overflow-hidden relative m-4">
      <div class="max-h-[calc(100vh_-_64px)] flex flex-col">
        <div class="flex justify-between px-6 py-4 leading-8">
          <h2 class="font-poppins font-semibold text-xl">
            {{ language === 'indonesian' ? 'Perjanjian Pengguna' : 'User agreement' }}
          </h2>

          <div class="flex items-center">
            <span class="text-xs font-semibold">ID</span>
            <InputToggle @on-toggle="toggleLanguage" />
            <span class="text-xs font-semibold">EN</span>
          </div>
        </div>

        <div class="overflow-y-auto">
          <div class="px-6 py-2 text-sm" v-html="text"></div>
        </div>

        <div class="mb-2">
          <div class="flex mx-4 my-4 justify-between border p-2 cursor-pointer items-center" @click="toggleAccept" data-cy="explicit-eula-sign">
            <p class="text-base m-0"> I acknowledge</p>

            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="w-5 h-5 sm:h-9 sm:w-9"
              :class="{ 'text-blue-600': accept, 'text-gray-400': !accept }"
              viewBox="0 0 20 20"
              fill="currentColor"
            >
              <path
                fill-rule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                clip-rule="evenodd"
              />
            </svg>
          </div>
          <div class="w-full flex justify-end">
            <button
              @click="agreeEula"
              type="button"
              data-cy="explicit-eula-accept"
              class="cursor-pointer my-auto border border-gray-200 bg-gray-200 text-gray-700 rounded-md px-4 py-2 m-2 transition duration-500 ease select-none hover:bg-gray-300 focus:outline-none focus:shadow-outline"
            >
              {{ language === 'indonesian' ? 'MENERIMA' : 'ACCEPT' }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { useAgreeToEula, useUsersEula } from '@/api/user';
import { computed, ref, watch } from 'vue';

const props = defineProps<{
  show: boolean;
  isAuthenticated: boolean;
}>();

const emit = defineEmits(['toggleUserEula']);

const language = ref('indonesian');
const accept = ref(false);

const toggleAccept = () => {
  accept.value = !accept.value;
};

const { mutateAsync: signEula, isSuccess: isSignEulaSuccess } = useAgreeToEula();
const agreeEula = () => {
  if (accept.value) {
    signEula({ variables: { input: { eulaId: eulaId.value } } });
  }
};
watch(isSignEulaSuccess, (isSignEulaSuccess) => {
  if (isSignEulaSuccess) {
    emit('toggleUserEula');
  }
});

const toggleLanguage = () => {
  language.value = language.value === 'indonesian' ? 'english' : 'indonesian';
  document.getElementById(language.value)?.scrollIntoView();
};

const { data: userEula } = useUsersEula({
  enabled: computed(() => props.isAuthenticated),
});
const text = computed(() => userEula.value?.userEula.document ?? '');
const eulaId = computed(() => userEula.value?.userEula.id ?? '');
</script>
