import waitFor from 'p-wait-for';
import { h, Ref } from 'vue';

export class NaverAuth {
  clientId: string;
  clientSecret: string;
  uiState: any;

  constructor(clientId: string, clientSecret: string, uiState: any) {
    this.clientId = clientId;
    this.clientSecret = clientSecret;
    this.uiState = uiState;
  }

  createScriptTag(callbackUrl: string, naverLoginButton: Ref<any>) {
    const script = document.createElement('script');
    script.id = NaverAuth.scriptId;
    script.src = NaverAuth.scriptSrc;
    script.defer = true;
    script.async = true;
    script.onload = () => {
      const naver = (window as any).naver;
      if (!naver) {
        throw new Error('Error: naver script is not installed properly');
      }

      const naverLogin = new naver.LoginWithNaverId({
        callbackUrl,
        clientId: this.clientId,
        isPopup: false,
        loginButton: { color: 'green', type: 3, height: 60 },
      });

      naverLogin.init();

      naverLoginButton.value.el.firstChild.href = naverLogin.generateAuthorizeUrl();
    };
    return script;
  }

  createButtonTag() {
    const naverId = h('div', {
      id: NaverAuth.buttonId,
      style: {
        position: 'absolute',
        top: '-10000px',
      },
    });

    return naverId;
  }

  generateLogoutUrl(token: string) {
    return `https://nid.naver.com/oauth2.0/token?grant_type=delete&client_id=${this.clientId}&client_secret=${this.clientSecret}&access_token=${token}&service_provider=NAVER`;
  }

  static userLogin(naverLoginButton: any) {
    if (!document || !naverLoginButton.firstChild) return;
    naverLoginButton.firstChild.click();
  }

  static get naverQueryParams() {
    return ['access_token', 'state', 'token_type', 'expires_in'];
  }

  static get scriptSrc() {
    return 'https://static.nid.naver.com/js/naveridlogin_js_sdk_2.0.2.js';
  }

  static get scriptId() {
    return 'naver-login-sdk';
  }

  static get buttonId() {
    return 'naverIdLogin';
  }
}
