import { SocialLogin } from '@pi-xcels/utils';
import { Buffer } from 'buffer';
import JsCookie from 'js-cookie';
import { computed, ref, watch } from 'vue';
import { ENV } from './env';
import { useLocalStorageManager } from './localstorage';
import { debugLog } from './logger';

const jsCookieConfig: JsCookie.CookieAttributes = {
  domain: ENV.NX_DOMAIN,
  ...(window.location.hostname === 'localhost' && { domain: 'localhost' }),
  expires: 30,
  path: '/',
  secure: window.location.hostname !== 'localhost', // cookie will be sent only over HTTPS except on localhost
  sameSite: 'Strict',
};
export const jsCookies = JsCookie.withAttributes(jsCookieConfig);

interface DecodedToken {
  provider: 'piperks' | 'anonymous';
  loginProvider: SocialLogin;
  token: string;
}

function isValidJSONBase64(base64string: string) {
  if (!isValidBase64(base64string)) {
    return false;
  }

  try {
    const value = decodeToken(base64string);
    return typeof value === 'object' && value !== null;
  } catch (_) {
    return false;
  }
}

function isValidBase64(base64String: string) {
  return Buffer.from(base64String, 'base64').toString('base64') === base64String;
}

function encodeToken(tokenObject: any) {
  return Buffer.from(JSON.stringify(tokenObject), 'utf-8').toString('base64');
}

function decodeToken(encodedToken: string) {
  return JSON.parse(Buffer.from(encodedToken, 'base64').toString('utf-8'));
}

function updateTokenCookie(data: any) {
  if (!getTokenCookie()) return;
  const tokenCookie = getTokenCookie();
  if (tokenCookie && isValidJSONBase64(tokenCookie)) {
    debugLog('updateTokenCookie: ', data);
    let token = decodeToken(tokenCookie);

    // Append new data to current token cookie
    Object.entries(data).forEach((entry) => {
      const [key, value] = entry;
      token[key] = value;
    });

    token = encodeToken(token);
    setTokenCookie(token);
  }
}

function getTokenCookie(): string | undefined {
  const tokenCookie = jsCookies.get('token');
  if (tokenCookie && tokenRef.value !== tokenCookie) {
    tokenRef.value = tokenCookie;
  }
  return tokenCookie;
}

function removeTokenCookie() {
  debugLog('removeTokenCookie');
  jsCookies.remove('token', { path: '/', domain: ENV.NX_DOMAIN });
  jsCookies.remove('token', { path: '/' });
  tokenRef.value = '';
}

function setTokenCookie(token: string) {
  debugLog('setTokenCookie: ', token);
  jsCookies.set('token', token);
  tokenRef.value = token;
}

function setOAuthStateValue(value: string) {
  jsCookies.set('oauthState', value);
}

function getOAuthStateValue() {
  return jsCookies.get('oauthState') || '';
}

// a reactive copy of the token cookie
const tokenRef = ref<string>();

const decodedToken = computed(() => {
  if (!tokenRef.value) return;
  if (!isValidJSONBase64(tokenRef.value)) return;

  return decodeToken(tokenRef.value) as DecodedToken;
});
const provider = computed(() => decodedToken.value?.provider);
const loginProvider = computed(() => decodedToken.value?.loginProvider);

/**
 * handle loginprovider of existing user
 * TODO: remove this code after September 2023. By this time, all active users should have persisted the updated token in cookie
 */
const { safelyGetItem, safelyRemoveItem } = useLocalStorageManager();
watch(
  loginProvider,
  (loginProvider) => {
    if (!loginProvider) return;
    if (Object.values(SocialLogin).includes(loginProvider)) return;
    // get legacy loginprovider from localstorage and update token cookie
    if (safelyGetItem('loginprovider')) {
      updateTokenCookie({ loginProvider: safelyGetItem('loginprovider') });
      safelyRemoveItem('loginprovider');
    }
  },
  { immediate: true }
);

export const useCookieManager = () => {
  if (tokenRef.value !== getTokenCookie()) tokenRef.value = getTokenCookie();

  return {
    updateTokenCookie,
    getTokenCookie,
    removeTokenCookie,
    setTokenCookie,
    isValidJSONBase64,
    isValidBase64,
    encodeToken,
    decodeToken,
    setOAuthStateValue,
    getOAuthStateValue,
    decodedToken,
    provider,
    loginProvider,
    tokenRef,
  };
};
