import { useFeatureStore } from '@/stores/feature';
import { SocialLogin, uniq } from '@pi-xcels/utils';
import once from 'just-once';

import * as Sentry from '@sentry/vue';
import { computed, watch } from 'vue';
import { useLocalStorageManager } from '../localstorage';
import { debugLog } from '../logger';

export const useLoginMethods = once(() => {
  const featureState = useFeatureStore();
  const LocalStorageManager = useLocalStorageManager();

  const allEnabledLoginMethods = computed<SocialLogin[]>(() => {
    const result =
      featureState.countryFeature?.supportedCountry
        ?.filter((country) => country.enabled)
        .map((country) => [...country.loginMethod.primary, ...country.loginMethod.secondary])
        .flat() ?? [];
    return uniq(result);
  });

  const loginMethodsOfSelectedCountry = computed(() => {
    const featuredLoginMethods = featureState.countryFeature?.supportedCountry?.find(
      (country) => country.abbreviation === LocalStorageManager.basecountry.value
    )?.loginMethod;

    // TODO: When "other" baseCountry feature added, use the "other" login method as fallback
    const fallbackLoginMethods = { primary: [SocialLogin.Google, SocialLogin.Facebook], secondary: [SocialLogin.Firebase] };

    if (!featuredLoginMethods?.primary.length || !featuredLoginMethods?.secondary.length) {
      Sentry.captureMessage('Country feature is not set properly');
    }
    return featuredLoginMethods || fallbackLoginMethods;
  });

  watch(
    allEnabledLoginMethods,
    (loginMethods) => {
      debugLog(`allEnabledLoginMethods: ${loginMethods.join(', ')}`);
    },
    { immediate: true }
  );

  watch(
    loginMethodsOfSelectedCountry,
    (loginMethods) => {
      debugLog(`loginMethodsOfSelectedCountry: ${loginMethods.primary.join(', ')}, ${loginMethods.secondary.join(', ')}`);
    },
    { immediate: true }
  );

  const isEnabled = (loginMethod: SocialLogin) => Object.values(loginMethodsOfSelectedCountry.value).flat().includes(loginMethod);

  return {
    loginMethodsOfSelectedCountry,
    allEnabledLoginMethods,
    isKakaoEnabled: computed(() => isEnabled(SocialLogin.Kakao)),
    isNaverEnabled: computed(() => isEnabled(SocialLogin.Naver)),
    isGoogleEnabled: computed(() => isEnabled(SocialLogin.Google)),
    isFacebookEnabled: computed(() => isEnabled(SocialLogin.Facebook)),
    isFirebaseEnabled: computed(() => isEnabled(SocialLogin.Firebase)),
    isLineEnabled: computed(() => isEnabled(SocialLogin.Line)),
  };
});
