import { i18n, LocalesWithLabel } from '@pi-xcels/utils';
import { computed } from 'vue';
import { useFeatureStore } from '../stores/feature';
import { useUiStore } from '../stores/ui';
import { useLocalStorageManager } from './localstorage';

export const useLanguageManager = () => {
  const uiState = useUiStore();
  const LocalStorageManager = useLocalStorageManager();
  const featureState = useFeatureStore();

  if (!i18n.global.locale.value)
    i18n.global.locale.value = convertFormat(LocalStorageManager.language.value?.substring(0, 2) || (navigator.language ?? '')?.substring(0, 2) || 'en');

  function setAppLanguage(lang: string) {
    i18n.global.locale.value = lang;
    uiState.language = lang;
    LocalStorageManager.setLanguage(lang);
  }

  function currentLanguage() {
    return i18n.global.locale.value;
  }

  function convertFormat(lang: string) {
    switch (lang) {
      case 'en':
        return 'en-US';
      case 'id':
        return 'id-ID';
      case 'ja':
        return 'ja-JP';
      case 'ko':
        return 'ko-KR';
      default:
        return 'en-US';
    }
  }

  const availableLanguagesLabels = computed(() => {
    return Object.values(LocalesWithLabel).filter((label) => {
      switch (true) {
        case label === '' && !featureState.koreanFeatureEnabled:
          return false;
        case label === '' && !featureState.japaneseFeatureEnabled:
          return false;
        default:
          return true;
      }
    });
  });

  const availableLanguages = computed(() => {
    return Object.keys(LocalesWithLabel).filter((key) => {
      switch (true) {
        case key === 'ko-KR' && !featureState.koreanFeatureEnabled:
          return false;
        case key === 'ja-JP' && !featureState.japaneseFeatureEnabled:
          return false;
        default:
          return true;
      }
    });
  });

  return { setAppLanguage, currentLanguage, convertFormat, availableLanguagesLabels, availableLanguages };
};
