import { useUserLogin } from '@/api/user';
import { useCookieManager } from '@/utils/cookie';
import { useFullScreenErrorMsg } from '@/utils/hooks/useErrorHandling';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { readonly, ref } from 'vue';
import { useI18n } from 'vue-i18n';

export function usePiperksLogin() {
  const { t } = useI18n();
  const CookieManager = useCookieManager();

  const {
    mutateAsync: piperksLogin,
    isLoading: isPiperksLoginLoading,
    error: piperksLoginError,
    isSuccess: isPiperksLoginSuccess,
    isError: isPiperksLoginError,
    data: piperksLoginData,
  } = useUserLogin();

  useFullScreenErrorMsg(
    piperksLoginError,
    readonly(
      ref({
        id: 'piperks-login-error',
        title: t('Error.LoginUnsuccessful.Title'),
        body: t('Error.LoginUnsuccessful.Body'),
        btnTxt: t('Error.BtnTextOK'),
        btnAction: () => {
          CookieManager.removeTokenCookie();
          window.location.reload();
        },
        icon: errorIcon,
      })
    )
  );

  return {
    piperksLogin,
    isPiperksLoginLoading,
    isPiperksLoginSuccess,
    isPiperksLoginError,
    piperksLoginError,
    piperksLoginData,
  };
}
