export const getDefaultCurrencyCodeByCountry = (country: string): string => {
  switch (country.toLowerCase()) {
    case 'id':
      return 'IDR';
    case 'kr':
      return 'KRW';
    case 'jp':
      return 'JPY';
    default:
      throw new Error(`Unhandled country ${country} in getDefaultCurrencyCodeByCountry`);
  }
};
