import { useUserLogin } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useUiStore } from '@/stores/ui';
import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { GoogleAuth, SocialLogin } from '@pi-xcels/utils';
import { useMutation } from '@tanstack/vue-query';
import EventEmitter from 'events';
import { readonly, ref, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import { useLoginMethods } from '../hooks/useLoginMethods';

const callbackEventKey = 'google-auth-callback';
const eventEmitter = new EventEmitter();

const googleScript = ref<HTMLScriptElement>();
const googleAuth = ref<GoogleAuth>();

export function useGoogleAuthCallback(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const { isGoogleEnabled } = useLoginMethods();
  const uiState = useUiStore();

  // Insert Google SDK
  watchEffect(() => {
    if (!isGoogleEnabled.value) return;
    // Create google login script
    if (!googleAuth.value) {
      googleAuth.value = new GoogleAuth(ENV.NX_GOOGLE_CLIENT_ID, uiState);
    }
    if (!googleScript.value) {
      googleScript.value = googleAuth.value.createScriptTag((response: any) => {
        eventEmitter.emit(callbackEventKey, response?.credential);
      });
      document.head.appendChild(googleScript.value);
    }
  });

  // Re-render google login button if needed when
  // the login dialog is shown to the user
  watchEffect(() => {
    if (!googleAuth.value || !googleScript.value || !isGoogleEnabled.value) return;
    if (uiState.showLoginDialog) {
      googleAuth.value.renderGoogleButton();
    }
  });

  const { mutateAsync: onResponse } = useGoogleAuthResponseHandler(piperksLogin);

  return {
    handleGoogleAuthCallback() {
      eventEmitter.on(callbackEventKey, (credential) => {
        onResponse(credential);
      });
    },
    googleScript,
    googleAuth,
  };
}

export function useGoogleAuthResponseHandler(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const CookieManager = useCookieManager();
  const { t } = useI18n();
  const alertState = useAlertStore();

  const googleAuthResponseHandler = useMutation({
    mutationFn: async (accessToken: any) => {
      if (!accessToken) throw new Error('Google login failed');
      const bearerTokenForLogin = CookieManager.encodeToken({ token: accessToken, provider: SocialLogin.Google });
      await piperksLogin({
        httpHeaders: {
          authorization: `Bearer ${bearerTokenForLogin}`,
        },
      });
    },
  });

  const { error } = googleAuthResponseHandler;
  useModalErrorMsg(
    error,
    readonly(
      ref({
        id: 'google-login-error',
        title: t('Error.SocialLoginFailed.Title', { provider: 'Google' }),
        body: t('Error.SocialLoginFailed.Body'),
        btnTxt: t('Error.BtnTextOK'),
        btnAction() {
          alertState.removeAlertMessage('google-login-error');
        },
      })
    )
  );

  return googleAuthResponseHandler;
}
