import { useUserLogin } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { debugLog } from '@/utils/logger';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { FacebookAuth, SocialLogin } from '@pi-xcels/utils';
import { useMutation } from '@tanstack/vue-query';
import waitFor from 'p-wait-for';
import { readonly, ref, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import { useLoginMethods } from '../hooks/useLoginMethods';

const facebookScript = ref<HTMLScriptElement>();
const facebookAuth = ref<FacebookAuth>();

export function useFacebookAuthCallback(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const { isFacebookEnabled } = useLoginMethods();
  const CookieManager = useCookieManager();

  // Insert Facebook SDK
  watchEffect(() => {
    if (!isFacebookEnabled.value) return;
    if (!facebookAuth.value) facebookAuth.value = new FacebookAuth(ENV.NX_FACEBOOK_APP_ID, ENV.NX_FACEBOOK_APP_VERSION);
    if (!facebookScript.value) {
      facebookScript.value = facebookAuth.value.createScriptTag();
      document.head.appendChild(facebookScript.value);
    }
  });

  const onSuccess = async (accessToken: string) => {
    const bearerTokenForLogin = CookieManager.encodeToken({ token: accessToken, provider: SocialLogin.Facebook });
    await piperksLogin({
      httpHeaders: {
        authorization: `Bearer ${bearerTokenForLogin}`,
      },
    });
  };
  const mutation = useHandleFacebookAuthCallback({
    onSuccess,
  });
  return { handleFacebookAuthCallback: mutation, facebookAuth, facebookScript };
}

async function handleFacebookAuthCallback(): Promise<string> {
  await waitFor(() => typeof FB !== 'undefined');
  await waitFor(
    () =>
      new Promise((resolve) => {
        FB.getLoginStatus((response: fb.StatusResponse) => resolve(response?.status === 'connected'));
      }),
    { interval: 300 }
  );
  return new Promise((resolve) => {
    FB.getLoginStatus((response: fb.StatusResponse) => {
      const accessToken = response?.authResponse?.accessToken;
      resolve(accessToken);
    });
  });
}

function useHandleFacebookAuthCallback({ onSuccess }: { onSuccess: (d: any) => any }) {
  return useMutation({
    mutationKey: [MutationKeys.HandleFacebookAuthCallback],
    mutationFn: handleFacebookAuthCallback,
    onSuccess,
  });
}

export function useFacebookLogin() {
  const { isFacebookEnabled } = useLoginMethods();
  const { t } = useI18n();
  const alertState = useAlertStore();

  const facebookLogin = useMutation({
    onSuccess() {
      debugLog('facebookLogin success');
      // handleFacebookAuthCallback should be called after this
    },
    mutationKey: [MutationKeys.FacebookLogin],
    mutationFn: (): Promise<true> =>
      new Promise((resolve, reject) => {
        if (!isFacebookEnabled.value) {
          reject(new Error('Facebook login is not enabled'));
          return;
        }
        FacebookAuth.userLogin((authResponse) => {
          if (authResponse) resolve(true);
          else reject(new Error('Facebook login failed'));
        });
      }),
  });

  const { error: facebookLoginError } = facebookLogin;
  useModalErrorMsg(
    facebookLoginError,
    readonly(
      ref({
        id: 'facebook-login-error',
        title: t('Error.SocialLoginFailed.Title', { provider: 'Facebook' }),
        body: t('Error.SocialLoginFailed.Body'),
        btnTxt: t('Error.BtnTextOK'),
        btnAction() {
          alertState.removeAlertMessage('facebook-login-error');
        },
      })
    )
  );

  return { facebookLogin };
}
