<template>
  <div class="grid bg-white h-full w-full relative" :class="isModal ? 'grid-rows-[auto,1fr]' : 'grid-rows-[1fr,auto,1fr]'">
    <div class="row-start-1" v-if="shouldShowBackButton">
      <div class="text-left absolute cursor-pointer" :class="isModal ? 'top-0 left-0' : 'top-6 left-6'">
        <button data-cy="login-back-button" @click="handleBackButton">
          <font-awesome-icon icon="fa-solid fa-chevron-left" class="w-6 h-6" />
        </button>
      </div>
    </div>
    <div class="flex flex-col items-center row-start-2 w-full" :class="!isModal && 'px-8'" data-cy="login-page">
      <LogoAndTitle :title="$t('Login.Title')" :isModal="isModal" />
      <div class="flex flex-col w-full" :class="isModal ? 'py-3.5' : 'py-5'">
        <PrimaryLogin
          v-if="loginOption === 'social'"
          :login-country="loginCountry"
          :login-method="loginMethods.primary"
          :show-login-dialog="uiState.showLoginDialog"
          :google-login-loading="uiState.googleLoginLoading"
          @toggle-login-option="toggleLoginOption"
          @set-google-login-button="(button: any) => {uiState.googleLoginButton = button}"
          @onclick-function="(method: string) => handleLoginClick(method)"
        />

        <SecondaryLogin
          v-else
          :login-country="loginCountry"
          :login-method="loginMethods.secondary"
          :phone-code="phoneCodes"
          @handle-country-code-change="handleCountryCodeChange"
          @toggle-login-option="toggleLoginOption"
          @toggle-otp="toggleOTP"
          @onclick-function="(method: string) => handleLoginClick(method)"
        />
      </div>

      <div v-if="featureState.demoMode && !featureState.sandboxMode" class="flex my-5">
        <p class="mx-auto text-sm text-gray-500" data-cy="demo-version-text">Demo Version</p>
      </div>
      <div v-if="featureState.sandboxMode" class="flex my-5">
        <p class="mx-auto text-sm text-gray-500" data-cy="sandbox-version-text">Sandbox Version</p>
      </div>
    </div>

    <div class="flex flex-col justify-center row-start-3 mt-auto mb-10 px-5">
      <EulaAndPolicy v-if="country !== 'kr'" @toggle-eula="toggleEula" />
      <div class="w-64 h-0"></div>
    </div>
  </div>
  <OTP
    :show="showOtp"
    :fullPhoneNumber="fullPhoneNumber"
    :showMsgError="showMsgError"
    @toggle-otp="toggleOTP"
    @on-submit-otp="onSubmitOtp"
    @toggle-show-msg-error="toggleShowMsgError"
  />

  <AppLoader v-if="isAuthenticating || isVerifyingFirebasePhoneAuth || isAuthenticated" />
</template>

<script setup lang="ts">
import { useFirebasePhoneAuth, useVerifyFirebasePhoneAuth } from '@/firebase/account';
import { useAlertStore } from '@/stores/alert';
import { useFeatureStore } from '@/stores/feature';
import { useUiStore } from '@/stores/ui';
import { useAuthManager } from '@/utils/auth';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { useLoginMethods } from '@/utils/hooks/useLoginMethods';
import { debugLog } from '@/utils/logger';
import { SocialLogin } from '@pi-xcels/utils';
import { computed, onMounted, readonly, ref, unref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute, useRouter } from 'vue-router';

const props = defineProps<{
  country: string;
  isModal?: boolean;
}>();

const uiState = useUiStore();
const featureState = useFeatureStore();
const route = useRoute();
const router = useRouter();
const AuthManager = useAuthManager();
const { t } = useI18n({ useScope: 'global' });
const alertState = useAlertStore();

const shouldShowBackButton = computed(() => !(featureState.demoMode || featureState.sandboxMode) && (route.name === 'login' || loginOption.value === 'phone'));

const showEula = ref(false);
const showOtp = ref(false);
const loginOption = ref('social');
const phoneCountryCode = ref('+62');
const fullPhoneNumber = ref('');
const showMsgError = ref(false);

const loginCountry = ref('default');

const { googleAuth, isAuthenticating, isAuthenticated, facebookLogin, handleFirebaseAuthCallback, handleFacebookAuthCallback } = AuthManager;

const { loginMethodsOfSelectedCountry: loginMethods } = useLoginMethods();

const phoneCodes = computed(() => {
  return featureState.countryFeature?.supportedCountry
    ?.filter((el) => el.enabled)
    .reduce((acc, country) => {
      acc[country.abbreviation] = country.phoneCode;
      return acc;
    }, {} as Record<string, string>);
});

const handleLoginClick = (method: string) => {
  switch (method) {
    case SocialLogin.Facebook:
      facebookLogin.mutateAsync().then(() => {
        handleFacebookAuthCallback.mutate();
      });
      break;

    case SocialLogin.Line:
      AuthManager.handleLineLoginClick();
      break;

    case SocialLogin.Kakao:
      AuthManager.handleKakaoLoginClick();
      break;

    case SocialLogin.Naver:
      AuthManager.handleNaverLoginClick();
      break;

    default:
      break;
  }
};

const toggleEula = () => {
  showEula.value = !showEula.value;
  uiState.showEula = !uiState.showEula;
};

const handleBackButton = () => {
  if (loginOption.value === 'social') return router.back();
  toggleLoginOption();
};
const toggleLoginOption = () => {
  loginOption.value = loginOption.value === 'social' ? 'phone' : 'social';
  if (loginOption.value === 'social') {
    unref(googleAuth)!.renderGoogleButton();
  }
};

const { mutateAsync: firebasePhoneAuth, error: firebasePhoneAuthError } = useFirebasePhoneAuth();
useModalErrorMsg(
  firebasePhoneAuthError,
  readonly(
    ref({
      id: 'firebase-phone-auth-error',
      title: t('Error.OtpSendingFailed.Title'),
      body: t('Error.OtpSendingFailed.Body'),
      btnTxt: t('Error.BtnTextOK'),
      btnAction() {
        alertState.removeAlertMessage('firebase-phone-auth-error');
      },
    })
  )
);

const toggleOTP = (phoneNumber: string) => {
  if (!showOtp.value) {
    fullPhoneNumber.value = phoneCountryCode.value + phoneNumber;
    firebasePhoneAuth({
      recaptchaContainer: 'recaptchaContainer',
      phoneNumber: fullPhoneNumber.value,
    }).then((data) => {
      if (data) {
        showOtp.value = !showOtp.value;
      } else {
        debugLog('OTP SENT >>> ', data);
      }
    });
  } else {
    showOtp.value = !showOtp.value;
  }
};

const { mutateAsync: verifyFirebasePhoneAuth, isLoading: isVerifyingFirebasePhoneAuth } = useVerifyFirebasePhoneAuth();

const onSubmitOtp = (otp: any) => {
  verifyFirebasePhoneAuth({
    otpCode: otp,
  })
    .then((data: any) => {
      debugLog(' ~ file: Login.vue ~ line 95 ~ verifyFirebasePhoneAuth ~ data', data);
      showOtp.value = !showOtp.value; // close OTP Page after sign in to make sure it's not showing up when logout
      loginOption.value = 'social'; // resetting to social mode after logout
      handleFirebaseAuthCallback.mutate();
    })
    .catch(() => {
      showMsgError.value = true;
      otp = null;
    });
};

const toggleShowMsgError = () => {
  showMsgError.value = !showMsgError.value;
};

const handleCountryCodeChange = (e: any) => {
  phoneCountryCode.value = e.target.value;
};

watch(
  () => props.country,
  (newCountry) => {
    phoneCountryCode.value = phoneCodes.value[newCountry] || '+62';
  }
);

onMounted(() => {
  unref(googleAuth)!.renderGoogleButton();
});
</script>
