import { graphql } from '../../graphql-codegen';

export const utiliseCurrentReciptMutation = graphql(`
  mutation utiliseTransaction($data: TransactionCreateInput!) {
    utiliseTransaction(data: $data) {
      utilised
    }
  }
`);

export const userReceiptsQuery = graphql(`
  query userReceipts($limit: Int, $offset: Int, $filter: FilterReceipt) {
    userReceipts(limit: $limit, offset: $offset, filter: $filter) {
      receiptNo
      receipt
      printedAt
      state
      rawReceiptBinary
      id
      isBinaryReceipt
      companies {
        logos
        whatsappNumber
        locale
        currencyCode
      }
      transactionRef
    }
  }
`);

export const currentReceiptSubscription = graphql(`
  subscription currentReceipt($transactionRefs: [String!]) {
    currentReceipt(transactionRefs: $transactionRefs) {
      receipt
      companies {
        logos
        whatsappNumber
        baseCountry
        locale
        currencyCode
      }
      companyId
      baseCountry
      receiptNo
      transactionId
      state
      transactionRef
    }
  }
`);

export const clearCurrentReceiptMutation = graphql(`
  mutation clearCurrentReceipt {
    updateCurrentReceipt {
      userId
      currentReceipt
    }
  }
`);

export const getReceiptQuery = graphql(`
  query getReceipt($where: TransactionsWhereUniqueInput!, $mode: String) {
    getReceipt(where: $where, mode: $mode) {
      receipt
      rawReceiptBinary
      receiptNo
      state
      isBinaryReceipt
      transactionId
      companyId
      companies {
        logos
        whatsappNumber
        baseCountry
        locale
        currencyCode
      }
      transactionRef
      userId
    }
  }
`);

export const getReceiptTemplateQuery = graphql(`
  query getReceiptTemplate($where: ReceiptTemplateWhereInput!) {
    getReceiptTemplate(where: $where) {
      template
    }
  }
`);

export const createTransactionMutation = graphql(`
  mutation createTransaction($data: TransactionCreateInput!) {
    createTransaction(data: $data) {
      authTokenUid
      userId
      id
      utilised
      ref
      expired {
        isReceiptExpired
        isReceiptLinkExpired
      }
      isVoided
      isEmpTransaction
      accountId
    }
  }
`);

export const linkReceiptToUserMutation = graphql(`
  mutation linkReceiptToUser($where: ReceiptWhereUniqueInput!) {
    linkReceiptToUser(where: $where) {
      isReceiptLinked
      employee {
        isEmpIdAdded
        prompt
        expired
        toast
        latestEmpId
        existingEmpId
        isEmpIdUpdatable
      }
    }
  }
`);

export const userReceiptStoreQuery = graphql(`
  query userReceiptsStore {
    userReceiptsStore {
      stores {
        name
        id
      }
    }
  }
`);
