import { graphql } from '../../graphql-codegen';

export const userLoginMutation = graphql(`
  mutation login {
    login {
      token
      authProvider
      loginProvider
    }
  }
`);

export const updateUserInfoMutation = graphql(`
  mutation updateUserInfo($data: UserUpdateInput!) {
    updateUserInfo(data: $data) {
      id
      email
      phoneNo
      dob
      name
      address
      accountProvider
      accountType
      country
      events
      gender
      geoLocationEnabled
      language
      picture
      pushNotificationEnabled
      usersEula {
        id
      }
      pwa
      showEula
      isDemoExpired
      yob
      showDobGender
      countrySupport
      baseCountry
    }
  }
`);

export const updateUserMutation = graphql(`
  mutation user($data: UserCreateInput!, $where: UserWhereUniqueInput!) {
    user(data: $data, where: $where) {
      id
      email
      phoneNo
      dob
      name
      address
      accountProvider
      accountType
      showBaseCountry
      country
      gender
      geoLocationEnabled
      language
      picture
      pushNotificationEnabled
      usersEula {
        id
      }
      pwa
      showEula
      isDemoExpired
      yob
      showDobGender
      countrySupport
      baseCountry
    }
  }
`);

export const updateEmployeeMutation = graphql(`
  mutation updateEmployee($data: EmployeeUpdateInput!) {
    updateEmployee(data: $data) {
      id
      ref
    }
  }
`);

export const addUserDeviceMutation = graphql(`
  mutation addDevice($data: CreateUserDeviceInput!) {
    addDevice(data: $data) {
      device
    }
  }
`);
