import { ENV } from '@/utils/env';
import { NodeEnvironment } from '@/utils/validateEnv';
import { QueryKeys } from '@pi-xcels/graphql/constants';
import { SocialLogin, demoRemoteConfig, getFeatureFlag, productionRemoteConfig, sandboxRemoteConfig } from '@pi-xcels/utils';
import { useQuery } from '@tanstack/vue-query';
import { defineStore } from 'pinia';
import { computed } from 'vue';

interface CountryFeature {
  supportedCountry: SupportedCountry[];
}
interface SupportedCountry {
  name: string;
  abbreviation: string;
  enabled: boolean;
  countryFlagImg: string;
  phoneCode: string;
  loginMethod: {
    primary: SocialLogin[];
    secondary: SocialLogin[];
  };
}

// TODO: When "other" baseCountry feature added, use the "other" info as fallback
const countryFeatureFallback: CountryFeature = {
  supportedCountry: [
    {
      name: 'Indonesia',
      abbreviation: 'id',
      enabled: true,
      countryFlagImg: 'https://public-static-assets-piperks.s3.ap-southeast-1.amazonaws.com/id.svg',
      phoneCode: '+62',
      loginMethod: { primary: [SocialLogin.Google, SocialLogin.Facebook], secondary: [SocialLogin.Firebase] },
    },
  ],
};

export const useFeatureStore = defineStore('feature', () => {
  const { data, isLoading } = useQuery({
    queryKey: [QueryKeys.GetFeatureFlag],
    queryFn: window.Cypress ? getMockFeatureFlag : getFeatureFlag,
  });

  return {
    isReady: computed(() => !!data.value),
    isLoading,
    feature: data,
    demoConfig: computed(() => (data.value?.piperksDemoPurgeConfig ? JSON.parse(data.value?.piperksDemoPurgeConfig?._value) : {})),
    demoMode: computed(() => data.value?.DEMO_MODE?._value === 'true' || ENV.NX_FORCE_DEMO_MODE),
    demoModeIsLoaded: computed(() => !!data.value || ENV.NX_FORCE_DEMO_MODE),
    demoModeIsLoading: computed(() => !ENV.NX_FORCE_DEMO_MODE && isLoading.value),
    loginPopupDelay: computed(() => Number(data.value?.LoginPopupDelay?._value) || 5000),
    koreanFeatureEnabled: computed(
      () => data.value?.KoreanFeatureEnabled?._value === 'true' || [NodeEnvironment.Development, NodeEnvironment.Test].includes(ENV.NX_ENVIRONMENT)
    ),
    countryFeature: computed(
      () => ((data.value?.CountryFeature._value ? JSON.parse(data.value.CountryFeature._value) : null) as CountryFeature) || countryFeatureFallback
    ),
    japaneseFeatureEnabled: computed(() => data.value?.JapaneseFeatureEnabled?._value === 'true'),
    sandboxMode: computed(() => ENV.NX_SANDBOX_MODE),
  };
});

const getMockFeatureFlag = async (): Promise<Record<string, any>> => {
  let mockFeatFlag: Record<string, any>;
  if (ENV.NX_FORCE_DEMO_MODE) {
    mockFeatFlag = demoRemoteConfig;
  } else if (ENV.NX_SANDBOX_MODE) {
    mockFeatFlag = sandboxRemoteConfig;
  } else {
    mockFeatFlag = productionRemoteConfig;
  }

  const mockFeatFlagRes: Record<string, any> = {};
  for (const [key, value] of Object.entries(mockFeatFlag)) {
    mockFeatFlagRes[key] = typeof value === 'string' ? { _value: value } : { _value: JSON.stringify(value) };
  }

  return mockFeatFlagRes;
};
