export const useImplicitEula = (options?: { enabled: Ref<boolean> }) => {
  return useQuery({
    queryKey: [QueryKeys.ImplicitEula],
    queryFn: () =>
      apolloClient
        .query({
          query: implicitEulaQuery,
        })
        .then(handleApolloQueryResponse),
    refetchOnWindowFocus: false,
    ...options,
  });
};

