import { useRequestFcmPermission, useUpdateUserInfo } from '@/api/user';
import { UserUpdateInput } from '@pi-xcels/graphql/types';
import { watch } from 'vue';
import { useLocalStorageManager } from './localstorage';
import { alwaysLog } from './logger';

export const useNotificationManager = () => {
  const LocalStorageManager = useLocalStorageManager();
  const { mutateAsync: userUpdate } = useUpdateUserInfo();
  const { mutateAsync: requestFCMPermission, data: fcmPermissionData, isSuccess: isFcmPermissionSuccess } = useRequestFcmPermission();

  function shouldAskNotificationPerm() {
    return 'Notification' in window && Notification.permission !== 'granted' && Notification.permission !== 'denied';
  }

  function isNotificationGranted() {
    return 'Notification' in window && Notification.permission === 'granted';
  }

  function isPushNotificationNotSupported() {
    return !('Notification' in window) && LocalStorageManager.pushNotification.value !== 'notSupported';
  }

  function requestNotificationPermission({
    allowedCallback,
    disallowedCallback,
  }: {
    allowedCallback?: (permission: NotificationPermission) => void;
    disallowedCallback?: (permission: NotificationPermission) => void;
  }) {
    Notification.requestPermission().then(async (permission) => {
      switch (permission) {
        case 'granted':
          allowNotificationPermission();
          if (allowedCallback) allowedCallback(permission);
          break;
        case 'denied':
        case 'default':
        default:
          disallowNotificationPermission(permission);
          if (disallowedCallback) disallowedCallback(permission);
          break;
      }
    });
  }

  function allowNotificationPermission() {
    requestFCMPermission();
  }
  watch([fcmPermissionData, isFcmPermissionSuccess], ([fcmPermissionData, isFcmPermissionSuccess]) => {
    if (!isFcmPermissionSuccess) return;
    if (fcmPermissionData?.token)
      userUpdate({
        variables: {
          data: {
            fcmToken: fcmPermissionData.token,
            pushNotificationEnabled: true,
          },
        },
      });
    else
      userUpdate({
        variables: {
          data: {
            pushNotificationEnabled: true,
          },
        },
      });
  });

  async function disallowNotificationPermission(permission: NotificationPermission) {
    alwaysLog('Unable to get permission to notify.');

    const updateData: UserUpdateInput = {};

    if (permission === 'denied') {
      updateData.pushNotificationEnabled = false;
    }

    userUpdate({
      variables: { data: updateData },
    });
  }

  function checkPushNotificationSupport() {
    if (isPushNotificationNotSupported()) {
      LocalStorageManager.setPushNotification('notSupported');
    }
  }

  return {
    shouldAskNotificationPerm,
    isNotificationGranted,
    isPushNotificationNotSupported,
    requestNotificationPermission,
    allowNotificationPermission,
    disallowNotificationPermission,
    checkPushNotificationSupport,
  };
};
