import { useUserLogin } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import normalizeQueryValue from '@/utils/route';
import { KakaoAuth, SocialLogin } from '@pi-xcels/utils';
import { useMutation } from '@tanstack/vue-query';
import waitFor from 'p-wait-for';
import { Ref, computed, readonly, ref, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute, useRouter } from 'vue-router';
import { useLoginMethods } from '../hooks/useLoginMethods';

const kakaoScript = ref<HTMLScriptElement>();
const kakaoAuth = ref<KakaoAuth>();

export function useKakaoLogin(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const { isKakaoEnabled } = useLoginMethods();
  kakaoAuth.value = kakaoAuth.value ?? new KakaoAuth(ENV.NX_KAKAO_CLIENT_ID, ENV.NX_KAKAO_REST_API_KEY);

  const route = useRoute();

  // Insert Kakao SDK
  watchEffect(() => {
    if (!isKakaoEnabled.value) return;
    if (!kakaoScript.value) {
      kakaoScript.value = kakaoAuth.value!.createScriptTag();
      document.head.appendChild(kakaoScript.value);
    }
  });

  const { mutateAsync: onGetAccessTokenSuccess } = useKakaoAccessTokenHandler(piperksLogin);

  const getAccessToken = useGetKakaoAccessToken({
    onSuccess: onGetAccessTokenSuccess,
  });

  const handleKakaoAuthCallback = () => {
    if (!route.query?.code) return;
    const code = normalizeQueryValue(route.query.code);
    getAccessToken.mutate({
      redirectUri: `${ENV.NX_ROOT_URL}/auth/callback/kakao`,
      code,
      clientId: ENV.NX_KAKAO_REST_API_KEY,
    });
  };
  return { handleKakaoAuthCallback, ...getAccessToken, kakaoAuth, kakaoScript };
}

function useKakaoAccessTokenHandler(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const CookieManager = useCookieManager();

  const mutation = useMutation({
    mutationFn: async (tokenInfo: any) => {
      await waitFor(() => typeof (window as any).Kakao !== 'undefined');
      if ((window as any).Kakao.isInitialized) {
        (window as any).Kakao.Auth.setAccessToken(tokenInfo.access_token);
      }
      const bearerTokenForLogin = CookieManager.encodeToken({ token: tokenInfo.access_token, provider: SocialLogin.Kakao });
      await piperksLogin({
        httpHeaders: {
          authorization: `Bearer ${bearerTokenForLogin}`,
        },
      });
    },
  });
  const { error } = mutation;
  useKakaoModalErrorMsg(error);
  return mutation;
}

function useGetKakaoAccessToken({ onSuccess }: { onSuccess?: (data: any) => any }) {
  const mutation = useMutation({
    mutationFn: computed(() => kakaoAuth.value?.getKakaoAccessToken),
    onSuccess,
  });
  const { error } = mutation;
  useKakaoModalErrorMsg(error);
  return mutation;
}

function useKakaoModalErrorMsg(error: Readonly<Ref<unknown>>) {
  const { t } = useI18n();
  const alertState = useAlertStore();
  const router = useRouter();

  return useModalErrorMsg(
    error,
    readonly(
      ref({
        id: 'kakao-login-error',
        title: t('Error.SocialLoginFailed.Title', { provider: 'Kakao' }),
        body: t('Error.SocialLoginFailed.Body'),
        btnTxt: t('Error.BtnTextOK'),
        btnAction() {
          alertState.removeAlertMessage('kakao-login-error');
          router.push('/auth/login');
        },
      })
    )
  );
}
