import { useUserLogin } from '@/api/user';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { useMutation } from '@tanstack/vue-query';

export function useAnonymousLogin(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  function anonymousLogin() {
    return piperksLogin({
      httpHeaders: {
        ['x-pixcels-session']: 'anonymous',
      },
    });
  }
  return useMutation({
    mutationKey: [MutationKeys.AnonymousLogin],
    mutationFn: anonymousLogin,
  });
}
