<template>
  <ModalBackdrop :show="show" margin="m-6" padding="p-6 md:p-8">
    <div data-cy="login-modal" class="w-full">
      <LoginView :country="loginCountry" :is-modal="true" />
    </div>
  </ModalBackdrop>
</template>

<script setup lang="ts">
import { useFeatureStore } from '@/stores/feature';
import { useLocalStorageManager } from '@/utils/localstorage';
import { onMounted, ref } from 'vue';
import { useRoute } from 'vue-router';

defineProps<{
  show: boolean;
}>();

const featureState = useFeatureStore();
const route = useRoute();
const LocalStorageManager = useLocalStorageManager();

const loginCountry = ref('default');

onMounted(() => {
  loginCountry.value = (() => {
    let baseCountry = '';
    switch (true) {
      // Speical case for sandbox mode
      case featureState.sandboxMode:
        baseCountry = 'id';
        break;

      // Set login country by url
      case route.path === '/receipt' && Boolean(route.query?.location):
        baseCountry = route.query.location as string;
        break;

      // Set login country by selected country option
      case !!LocalStorageManager.basecountry.value:
        return LocalStorageManager.basecountry.value;

      default:
        return 'default';
    }

    if (baseCountry !== '') LocalStorageManager.setBaseCountry(baseCountry);
    return baseCountry;
  })();
});
</script>
