import { computed, readonly, ref } from 'vue';
import { useRouter } from 'vue-router';

export const useRouterReady = () => {
  const router = useRouter();
  const isRouterNotReady = ref(true);
  (async () => {
    await router.isReady();
    isRouterNotReady.value = false;
  })();
  return {
    isRouterNotReady: readonly(isRouterNotReady),
    isRouterReady: computed(() => !isRouterNotReady.value),
  };
};
