<template>
  <ModalBackdrop :show="show" :margin="''" :padding="'p-8'">
    <div class="flex flex-col justify-start gap-y-5" data-cy="korean-eula-modal">
      <div class="flex flex-wrap items-center justify-start justify-items-start gap-x-4">
        <img class="w-14" :src="piperksLogo" alt="Piperks Logo" data-cy="login-logo" />
        <span class="font-semibold"> Piperks </span>
      </div>
      <div>
        {{ $t('KoreanExplicit.Title') }}
      </div>
      <div class="flex flex-col text-base font-semibold gap-y-3">
        <Divider />

        <label>
          <input type="checkbox" v-model="checkAllVal" @change="toggleAll" data-cy="acceptall-korean-eula" />
          {{ $t('KoreanExplicit.AcceptAll') }}
        </label>
        <Divider />
      </div>
      <div class="flex flex-col gap-y-4">
        <div class="flex gap-x-14 justify-between" v-for="(item, itemID) in checkboxItem" :key="item.description">
          <label>
            <input type="checkbox" :value="itemID" v-model="checkedItem" />
            <span class="text-sm"> [{{ item.required && $t('KoreanExplicit.Required') }}] {{ item.description }} </span>
          </label>
          <span v-if="item.viewMore" class="flex text-xs items-center text-gray-500">
            <a :href="item.viewMore" target="_blank" rel="noopener noreferrer">
              <u>{{ $t('KoreanExplicit.ViewMore') }}</u>
            </a>
          </span>
        </div>
        <div class="flex justify-center mt-4">
          <button
            @click="emit('signKoreanEula')"
            class="bg-indigo-600 hover:bg-indigo-700 rounded-lg disabled:bg-indigo-300 py-2 w-full text-white"
            :disabled="disabledButton"
            data-cy="continue-korean-eula-button"
          >
            Continue
          </button>
        </div>
      </div>
    </div>
    <template #backdropContent>
      <div class="w-100 bg-white"></div>
      <div class="relative flex justify-center mt-2 text-gray-500">
        <button @click="emit('onCancel')" data-cy="cancel-korean-eula-button"><u>Cancel</u></button>
      </div>
    </template>
  </ModalBackdrop>
</template>
<script setup lang="ts">
import { ref, reactive, watchEffect } from 'vue';
import { i18n } from '@pi-xcels/utils';
import piperksLogo from '@/assets/images/piperks-logo.png';

defineProps<{
  show: boolean;
}>();

const emit = defineEmits(['signKoreanEula', 'onCancel']);

const { t } = i18n.global;
const checkboxItem = reactive({
  termOfUse: {
    description: t('KoreanExplicit.TermOfUse'),
    required: true,
    viewMore: 'https://www.pi-xcels.com/',
  },
  privacyPolicy: {
    description: t('KoreanExplicit.PrivacyPolicy'),
    required: true,
    viewMore: 'https://www.pi-xcels.com/',
  },
  ageConsent: {
    description: t('KoreanExplicit.AgeConsent'),
    required: true,
    viewMore: null,
  },
});

const checkedItem = ref<string[]>([]);
const disabledButton = ref(true);
const checkAllVal = ref(false);
const toggleAll = () => {
  if (checkAllVal.value) {
    checkedItem.value = Object.keys(checkboxItem);
  } else {
    checkedItem.value = [];
  }
};

const requiredCondition = Object.entries(checkboxItem).filter(([, val]) => val.required).length;

//Disable agree button if not all required item is checked.
watchEffect(() => {
  if (checkedItem.value.length >= requiredCondition) {
    checkAllVal.value = true;
    disabledButton.value = false;
  } else {
    disabledButton.value = true;
    checkAllVal.value = false;
  }
});
</script>
