import { logout as firebaseLogout } from '@/firebase/account';
import { useCookieManager } from '@/utils/cookie';
import { debugLog } from '@/utils/logger';
import { SocialLogin } from '@pi-xcels/utils';
import { ComputedRef, unref } from 'vue';
import { ENV } from '../env';
import { useLocalStorageManager } from '../localstorage';
import { useClearSessionAndRedirect } from './useClearSessionAndRedirect';
import { useNaverAuth } from './useNaverLogin';

export function useLogout({ loginProvider, onDone }: { loginProvider: ComputedRef<SocialLogin | undefined>; onDone: Function }) {
  const { naverAuth } = useNaverAuth();
  const CookieManager = useCookieManager();
  const LocalStorageManager = useLocalStorageManager();
  const { clearSessionAndRedirect } = useClearSessionAndRedirect();

  async function piperksLogout() {
    let loginProviderFallback = unref(loginProvider) ?? '';
    const isInvalidLoginProvider = !loginProviderFallback || !Object.values(SocialLogin).includes(loginProviderFallback as SocialLogin);
    if (isInvalidLoginProvider) {
      // this is a fallback for legacy login provider
      // TODO: remove this code after September 2023. By this time, all active users should have persisted the updated token in cookie
      loginProviderFallback = LocalStorageManager.safelyGetItem('loginprovider') as SocialLogin;
    }

    switch (unref(loginProviderFallback)) {
      case SocialLogin.Kakao:
        if ((window as any).Kakao.Auth.getAccessToken()) {
          (window as any).Kakao.Auth.logout();
        }
        break;
      case SocialLogin.Naver:
        if (ENV.NX_ROOT_URL.includes('localhost')) {
          debugLog('Access deep link below in new tab for requesting of delete Naver access token');
          debugLog(unref(naverAuth)!.generateLogoutUrl(CookieManager.getTokenCookie() ?? ''));
        } else {
          await fetch(unref(naverAuth)!.generateLogoutUrl(CookieManager.getTokenCookie() ?? ''), { mode: 'no-cors' });
        }
        break;
      case SocialLogin.Facebook:
        if ('FB' in window) {
          FB.logout();
        }
        break;
      case SocialLogin.Firebase:
        await firebaseLogout();
        break;
      default:
        break;
    }

    clearSessionAndRedirect();

    onDone();
  }
  return { piperksLogout };
}
