import { graphql } from '../../graphql-codegen';

export const implicitEulaQuery = graphql(`
  query implicitEula {
    implicitEula {
      document
      id
    }
  }
`);

export const usersEulaQuery = graphql(`
  query userEula {
    userEula(where: { ref: "English-Indonesian-Eula-v1" }) {
      document
      id
    }
  }
`);

export const agreeEulaMutation = graphql(`
  mutation signEula($input: UsersEulaCreateInput!) {
    signEula(input: $input) {
      id
      eulaId
      userId
    }
  }
`);
