import { ENV } from '@/utils/env';
import * as Sentry from '@sentry/vue';
import { QueryClient as VueQueryClient, VueQueryPlugin, VueQueryPluginOptions } from '@tanstack/vue-query';
import { createPinia } from 'pinia';
import { registerSW } from 'virtual:pwa-register';
import { createApp, h } from 'vue';
import { DynamicScroller, DynamicScrollerItem } from 'vue-virtual-scroller';
import VWave from 'v-wave';

import { i18n } from '@pi-xcels/utils';
import VueSocialSharing from 'vue-social-sharing';
import 'vue-virtual-scroller/dist/vue-virtual-scroller.css';
import App from './App.vue';
import './assets/base.css';
import router from './router';

import { useLocalStorageManager } from '@/utils/localstorage';
import { NodeEnvironment } from '@/utils/validateEnv';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faTelegram } from '@fortawesome/free-brands-svg-icons/faTelegram';
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons/faWhatsapp';
import { faQuestionCircle, faCopy as farCopy } from '@fortawesome/free-regular-svg-icons';
import { faCakeCandles } from '@fortawesome/free-solid-svg-icons/faCakeCandles';
import { faCartShopping } from '@fortawesome/free-solid-svg-icons/faCartShopping';
import { faChevronLeft } from '@fortawesome/free-solid-svg-icons/faChevronLeft';
import { faCaretDown } from '@fortawesome/free-solid-svg-icons/faCaretDown';
import { faChevronRight } from '@fortawesome/free-solid-svg-icons/faChevronRight';
import { faCircleCheck } from '@fortawesome/free-solid-svg-icons/faCircleCheck';
import { faCopy } from '@fortawesome/free-solid-svg-icons/faCopy';
import { faDownload } from '@fortawesome/free-solid-svg-icons/faDownload';
import { faEnvelope } from '@fortawesome/free-solid-svg-icons/faEnvelope';
import { faFilter } from '@fortawesome/free-solid-svg-icons/faFilter';
import { faFlag } from '@fortawesome/free-solid-svg-icons/faFlag';
import { faGlobe } from '@fortawesome/free-solid-svg-icons/faGlobe';
import { faHouse } from '@fortawesome/free-solid-svg-icons/faHouse';
import { faLocationDot } from '@fortawesome/free-solid-svg-icons/faLocationDot';
import { faPhone } from '@fortawesome/free-solid-svg-icons/faPhone';
import { faPlus } from '@fortawesome/free-solid-svg-icons/faPlus';
import { faReceipt } from '@fortawesome/free-solid-svg-icons/faReceipt';
import { faRestroom } from '@fortawesome/free-solid-svg-icons/faRestroom';
import { faRightFromBracket } from '@fortawesome/free-solid-svg-icons/faRightFromBracket';
import { faShareNodes } from '@fortawesome/free-solid-svg-icons/faShareNodes';
import { faUser } from '@fortawesome/free-solid-svg-icons/faUser';
import { faUserGroup } from '@fortawesome/free-solid-svg-icons/faUserGroup';
import { faWallet } from '@fortawesome/free-solid-svg-icons/faWallet';
import { faXmark } from '@fortawesome/free-solid-svg-icons/faXmark';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { createGtm } from '@gtm-support/vue-gtm';

library.add(
  faUser,
  faEnvelope,
  faPhone,
  faRestroom,
  faCakeCandles,
  faHouse,
  faFlag,
  faGlobe,
  faLocationDot,
  faUserGroup,
  faRightFromBracket,
  faDownload,
  faChevronLeft,
  faCaretDown,
  faShareNodes,
  faFilter,
  faChevronRight,
  faXmark,
  faPlus,
  faWhatsapp,
  faTelegram,
  faCopy,
  faWallet,
  faCartShopping,
  faReceipt,
  faCircleCheck,
  farCopy,
  faQuestionCircle
);

const app = createApp({
  render: () => h(App),
});

const pinia = createPinia();

// envs where sentry shouldn't run
const sentryUncheckEnv = [NodeEnvironment.Development, NodeEnvironment.Test];

if (ENV.NX_ENVIRONMENT && !sentryUncheckEnv.includes(ENV.NX_ENVIRONMENT)) {
  Sentry.init({
    app,
    environment: ENV.NX_ENVIRONMENT,
    dsn: ENV.NX_SENTRY_DSN,
    integrations: [
      new Sentry.BrowserTracing({
        routingInstrumentation: Sentry.vueRouterInstrumentation(router),
      }),
      new Sentry.Replay({
        maskAllText: true,
        blockAllMedia: false,
      }),
    ],
    // Set tracesSampleRate to 1.0 to capture 100%
    // of transactions for performance monitoring.
    // We recommend adjusting this value in production
    tracesSampleRate: ENV.NX_SENTRY_TRACE_SAMPLE_RATE,
    autoSessionTracking: true,
    replaysSessionSampleRate: ENV.NX_SENTRY_SESSION_SAMPLE_RATE,
    replaysOnErrorSampleRate: ENV.NX_SENTRY_ERROR_SAMPLE_RATE,
  });
}

app.use(pinia);
app.use(VueSocialSharing);
app.use(router);
app.use(i18n);

app.use(
  createGtm({
    id: ENV.NX_GTM_CONTAINER_ID,
    queryParams: {
      // Add URL query string when loading gtm.js with GTM ID (required when using custom environments)
      gtm_auth: ENV.NX_GTM_AUTH,
      gtm_preview: ENV.NX_GTM_PREVIEW,
      gtm_cookies_win: ENV.NX_GTM_COOKIES,
    },
    debug: ENV.DEV, // Whether or not display console logs debugs (optional)
    vueRouter: router, // Pass the router instance to automatically sync with router (optional)
  })
);

const vueQueryClient = new VueQueryClient(
  ENV.NX_TASK_TARGET_PROJECT === 'piperks-e2e'
    ? {
        // disable retry during testing to avoid long wait time
        defaultOptions: {
          mutations: {
            retryDelay: 0,
          },
          queries: {
            retryDelay: 0,
          },
        },
      }
    : undefined
);
const vueQueryPluginOptions: VueQueryPluginOptions = {
  queryClient: vueQueryClient,
};
app.use(VueQueryPlugin, vueQueryPluginOptions);

app.use(VWave);
app.component('font-awesome-icon', FontAwesomeIcon);
app.component('DynamicScroller', DynamicScroller);
app.component('DynamicScrollerItem', DynamicScrollerItem);

app.mount('#app');

registerSW({
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  onRegisteredSW(swScriptUrl: any, registration: any) {
    // intercept PWA installation
    const LocalStorageManager = useLocalStorageManager();
    window.addEventListener('appinstalled', () => {
      // Clear the deferredPrompt so it can be garbage collected
      (window as any).deferredPrompt = null;
      LocalStorageManager.setPwaInstallation('true');
    });

    window.addEventListener('beforeinstallprompt', (e) => {
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault();
      // Stash the event so it can be triggered later.
      (window as any).deferredPrompt = e;
    });
  },
});
