<template>
  <div
    v-if="uiState.showEula"
    @click.self="closeEula"
    class="fixed bg-black bg-opacity-20 flex justify-center items-center top-0 bottom-0 left-0 right-0 cursor-pointer"
  >
    <div class="max-w-md bg-white rounded-xl shadow-xl overflow-hidden relative m-4 cursor-auto">
      <div class="max-h-[calc(100vh_-_64px)] flex flex-col" data-cy="login-eula-dialog">
        <div class="flex justify-between px-6 py-4 leading-8">
          <h2 class="font-poppins font-semibold text-xl" data-cy="login-eula-title">
            {{ $t('Eula.Title') }}
          </h2>

          <div class="flex items-center">
            <span class="text-xs font-semibold">ID</span>
            <InputToggle data-cy="login-eula-toggle" @on-toggle="toggleLanguage" :checked="language === 'english'" />
            <span class="text-xs font-semibold">EN</span>
          </div>
        </div>

        <div class="overflow-y-auto">
          <div class="px-6 py-2 text-sm" v-html="eulaDocs"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
::v-deep() p {
  margin: 1em 0;
}
</style>

<script setup lang="ts">
import { useImplicitEula } from '@/api/user';
import { useUiStore } from '@/stores/ui';
import { useAuthManager } from '@/utils/auth';
import { useLanguageManager } from '@/utils/language';
import { computed, ref, unref } from 'vue';

const LanguageManager = useLanguageManager();
const { isAnonymous, isAuthenticated } = useAuthManager();

const uiState = useUiStore();
const language = ref(LanguageManager.currentLanguage() === 'en-US' ? 'english' : 'indonesian');

const toggleLanguage = () => {
  language.value = language.value === 'indonesian' ? 'english' : 'indonesian';
  LanguageManager.setAppLanguage(
    (() => {
      switch (language.value) {
        case 'indonesian':
          return 'id-ID';
        case 'english':
          return 'en-US';
        default:
          return 'en-US';
      }
    })()
  );

  document.getElementById(language.value)?.scrollIntoView();
};

const closeEula = () => {
  uiState.showEula = false;
};

const shouldEnableImplicitEula = computed(() => !unref(isAuthenticated) || unref(isAnonymous));

const { data: implicitEula } = useImplicitEula({
  enabled: shouldEnableImplicitEula,
});
const eulaDocs = computed(() => implicitEula.value?.implicitEula.document);
</script>
