import { useUserLogin } from '@/api/user';
import { useCookieManager } from '@/utils/cookie';
import { LineAuth, SocialLogin } from '@pi-xcels/utils';
import { ref } from 'vue';
import { useRoute } from 'vue-router';
import { ENV } from '../env';
import normalizeQueryValue from '../route';
import { useLoginMethods } from '../hooks/useLoginMethods';

const lineAuth = ref<LineAuth>();
export function useLineAuth() {
  return { lineAuth };
}

export function useLineAuthCallback(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const CookieManager = useCookieManager();
  const route = useRoute();
  const url = `${ENV.NX_ROOT_URL}/auth/callback/line`;
  lineAuth.value = lineAuth.value ?? new LineAuth(ENV.NX_LINE_CLIENT_ID, url, CookieManager);

  async function handleLineAuthCallback() {
    const lineAccessCode = normalizeQueryValue(route.query.code);

    const bearerTokenForLogin = CookieManager.encodeToken({
      token: lineAccessCode,
      provider: SocialLogin.Line,
    });

    piperksLogin({
      httpHeaders: {
        authorization: `Bearer ${bearerTokenForLogin}`,
      },
    });
  }
  return { handleLineAuthCallback, isLoading: false, lineAuth };
}
