import { Buffer } from 'buffer';
import { debugLog } from './logger';
import { useLocalStorage } from '@vueuse/core';

export const useLocalStorageManager = () => {
  const baseCountry = useLocalStorage('basecountry', '', {});
  const language = useLocalStorage('language', '', {});
  const PWAInstalled = useLocalStorage('PWAInstalled', '', {});
  const hashedReceipt = useLocalStorage('hashed_receipt', '', {});
  const pushNotification = useLocalStorage('push_notification', '', {});

  function generateReceiptTemplateCacheKey(companyName: string) {
    return `receipt_${Buffer.from(companyName, 'utf-8').toString('base64')}`;
  }
  function isLocalStorageValueExist(key: string): boolean {
    return (localStorage.getItem(key) ?? '') !== '';
  }

  function safelyGetItem(key: string): string {
    if (isLocalStorageValueExist(key)) {
      return localStorage.getItem(key)!;
    }

    debugLog(`WARN: '${key}' key doesnt exist in localstorage`);
    return '';
  }

  function safelyRemoveItem(key: string): void {
    if (isLocalStorageValueExist(key)) {
      localStorage.removeItem(key);
    }
  }

  function getReceiptTemplateCache(companyName: string) {
    return Buffer.from(safelyGetItem(generateReceiptTemplateCacheKey(companyName)), 'base64').toString('utf-8');
  }

  function removeBaseCountry(): void {
    debugLog('removeBaseCountry');
    baseCountry.value = '';
  }

  function removeLanguage(): void {
    debugLog('removeLanguage');
    language.value = '';
  }

  function removePwaInstallation(): void {
    debugLog('removePwaInstallation');
    PWAInstalled.value = '';
  }

  function removeHashedReceipt(): void {
    debugLog('removeHashedReceipt');
    hashedReceipt.value = '';
  }

  function removePushNotification(): void {
    debugLog('removePushNotification');
    pushNotification.value = '';
  }

  function setReceiptTemplateCache(companyName: string, receiptTemplateString: string) {
    localStorage.setItem(generateReceiptTemplateCacheKey(companyName), Buffer.from(receiptTemplateString, 'utf-8').toString('base64'));
  }

  function setBaseCountry(value: string): void {
    debugLog('setBaseCountry', value);
    baseCountry.value = value;
  }

  function setLanguage(value: string): void {
    debugLog('setLanguage', value);
    language.value = value;
  }

  function setPwaInstallation(value: string): void {
    debugLog('setPwaInstallation', value);
    PWAInstalled.value = value;
  }

  function setHashedReceipt(value: string): void {
    debugLog('setHashedReceipt', value);
    hashedReceipt.value = value;
  }

  function setPushNotification(value: string): void {
    debugLog('setPushNotification', value);
    pushNotification.value = value;
  }

  return {
    basecountry: baseCountry,
    language,
    PWAInstalled,
    hashedReceipt,
    pushNotification,
    generateReceiptTemplateCacheKey,
    isLocalStorageValueExist,
    safelyGetItem,
    safelyRemoveItem,
    getReceiptTemplateCache,
    removeBaseCountry,
    removeLanguage,
    removePwaInstallation,
    removeHashedReceipt,
    setReceiptTemplateCache,
    setBaseCountry,
    setLanguage,
    setPwaInstallation,
    setHashedReceipt,
    setPushNotification,
    removePushNotification,
  };
};
