import { ENV } from '@/utils/env';
import { useLocalStorageManager } from '@/utils/localstorage';
import { ApolloClient, ApolloLink, ApolloQueryResult, FetchResult, HttpLink, InMemoryCache, from, split } from '@apollo/client/core';
import { onError } from '@apollo/client/link/error';
import { RetryLink } from '@apollo/client/link/retry';
import { GraphQLWsLink } from '@apollo/client/link/subscriptions';
import { getMainDefinition } from '@apollo/client/utilities';
import { GraphQLServerErrorCode } from '@pi-xcels/graphql/constants';
import { SocialLogin } from '@pi-xcels/utils';
import * as Sentry from '@sentry/vue';
import { createClient } from 'graphql-ws';
import { unref } from 'vue';
import { useCookieManager } from './cookie';
import { debugLog } from './logger';

const authLink = new ApolloLink((operation, forward) => {
  const LocalStorageManager = useLocalStorageManager();
  const CookieManager = useCookieManager();
  operation.setContext(({ headers }: { headers: any }) => {
    const cookie = CookieManager.getTokenCookie();
    const headerObj = {
      headers: {
        ...(!headers?.authorization && cookie && { authorization: `Bearer ${cookie}` }),
        'x-pixcels-basecountry': LocalStorageManager.basecountry.value,
        ...headers,
      },
    };

    if (operation.operationName === 'login' && operation.variables?.isAnonymous) {
      headerObj.headers['x-pixcels-session'] = 'anonymous';
    }

    return headerObj;
  });
  return forward(operation);
});

const [apiProtocol, apiUrl] = ENV.NX_API_URL.split('://');
const wsLink = new GraphQLWsLink(
  createClient({
    url: `ws${apiProtocol === 'https' ? 's' : ''}://` + apiUrl,
    connectionParams: () => {
      const CookieManager = useCookieManager();
      return {
        authorization: `Bearer ${CookieManager.getTokenCookie()}`,
      };
    },
  })
);

const errorLink = onError(({ graphQLErrors, networkError, operation }) => {
  const CookieManager = useCookieManager();
  const { loginProvider } = CookieManager;
  // Logger
  if (ENV.DEV) {
    if (graphQLErrors) {
      graphQLErrors.forEach((e) => {
        const { message, locations, path, extensions } = e;
        debugLog(
          `[GraphQL error]: Message: ${message}, Location: ${JSON.stringify(locations)}, Path: ${path}, Operation: ${operation.operationName}, code: ${
            extensions?.code
          }`
        );
        if (extensions?.code && Object.values(GraphQLServerErrorCode).includes(extensions?.code as any)) {
          Sentry.captureException(e);
        }
      });
    }
    if (networkError) debugLog(`[Network error]: ${networkError}`);
  }

  if (graphQLErrors?.[0]?.extensions?.code === 'UNAUTHENTICATED') {
    const isPossiblyFromKorean = [SocialLogin.Kakao, SocialLogin.Naver].includes(unref(loginProvider)!);
    const currentPath = window.location.pathname;
    const currentURL = window.location.href;

    if (currentPath !== '/auth/login' && currentPath !== '/receipt') {
      const loginURL = `${window.location.origin}/auth/login${isPossiblyFromKorean ? '?location=kr' : ''}`;
      window.location.replace(loginURL);
      CookieManager.removeTokenCookie();
      return;
    }

    // This block handles the "/receipt" link when token is expired/invalid
    if (currentPath === '/receipt') {
      CookieManager.removeTokenCookie();
      // using window.location.replace() to maintain the current "receipt" link to avoid redirection to auth route due to cookies removal
      window.location.replace(currentURL);
      return;
    }

    CookieManager.removeTokenCookie();
  }
});

const httpLink = new HttpLink({
  uri: ENV.NX_API_URL,
});

const link = split(
  // split based on operation type
  ({ query }) => {
    const definition = getMainDefinition(query);
    return definition.kind === 'OperationDefinition' && definition.operation === 'subscription';
  },
  wsLink,
  httpLink
);

const cache = new InMemoryCache();

export const apolloClient = new ApolloClient({
  link: from([
    errorLink,
    new RetryLink(
      // disable retry during testing to avoid long wait time
      ENV.NX_TASK_TARGET_PROJECT === 'piperks-e2e'
        ? {
            delay: () => 0,
          }
        : undefined
    ),
    authLink,
    link,
  ]),
  cache,
});

export const handleApolloQueryResponse = <T>({ data, error, errors }: ApolloQueryResult<T>): T => {
  if (error || errors) throw error || errors;
  return data;
};

export const handleApolloMutationResponse = <T>({ data, errors }: FetchResult<T>): T | undefined | null => {
  if (errors) throw errors;
  return data;
};
