import { ENV } from '@/utils/env';
import { initializeApp } from 'firebase/app';
import { getAuth } from 'firebase/auth';
import { getMessaging, isSupported } from 'firebase/messaging';

const firebaseConfig = {
  apiKey: ENV.NX_FIREBASE_API_KEY,
  authDomain: ENV.NX_AUTH_DOMAIN,
  databaseURL: ENV.NX_DATABASE_URL,
  projectId: ENV.NX_PROJECT_ID,
  storageBucket: ENV.NX_STORAGE_BUCKET,
  messagingSenderId: ENV.NX_MESSAGING_SENDER_ID,
  appId: ENV.NX_APP_ID,
  measurementId: ENV.NX_MEASUREMENT_ID,
};

const firebaseApp = initializeApp(firebaseConfig);
const auth = getAuth(firebaseApp);
let messaging: any = null;
if (window.Cypress) {
  // we are running in Cypress
  // so disable captcha
  auth.settings.appVerificationDisabledForTesting = true;
}
isSupported()
  .then((support) => {
    if (support) {
      messaging = getMessaging(firebaseApp);
    }
  })
  .catch((error) => {
    console.error('Firebase Messaging Support', error);
  });

export { auth, messaging };
