<template>
  <div class="max-w-md mx-auto h-full">
    <div class="h-full">
      <RouterView v-slot="{ Component }">
        <component :is="Component"></component>
      </RouterView>
    </div>
    <BottomNavbar v-if="['/app/receipts', '/app/account'].includes($route.path)" />
  </div>

  <!-- PWA Installation Prompt -->
  <Prompt
    :show="pwaInstallPrompt && isProductionMode && !isIos"
    :message="$t('PWAPrompt.Description')"
    :button="$t('PWAPrompt.Install')"
    :use-backdrop="true"
    :use-close-button="true"
    @close-prompt="pwaInstallPrompt = false"
    @button-click="addToHomeScreen"
    :dataCyContainer="'pwa-install-drawer'"
    :dataCyButton="'pwa-install-button'"
  />

  <!-- Login Dialog -->
  <template v-if="uiState.showLoginDialog">
    <LoginModal :show="uiState.showLoginDialog" @close-modal="closeLoginDialog" />
  </template>

  <!-- Eula -->
  <Eula />
  <KoreanEula @sign-korean-eula="signKoreanEula" :show="isProductionMode && koreanEulaModal" @on-cancel="onCancelKREula" />
  <UsersEula :show="isProductionMode && showUserEula" :isAuthenticated="isAuthenticated" @toggle-user-eula="toggleUserEula" />

  <!-- Application Loading View -->
  <AppLoader v-if="isAuthenticating || featureState.isLoading || isRouterNotReady" />

  <AppToastMessages :alertMessages="alertState.alertMessages" @close="(id) => alertState.removeAlertMessage(id)" />
  <AppModalMessages :alertMessages="alertState.alertMessages" @close="(id) => alertState.removeAlertMessage(id)" />
  <AppFullPageMessages :alertMessages="alertState.alertMessages" @close="(id) => alertState.removeAlertMessage(id)" />
</template>

<script setup lang="ts">
import { useUpdateUserInfo } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useFeatureStore } from '@/stores/feature';
import { useUiStore } from '@/stores/ui';
import { useUserStore } from '@/stores/user';
import { useAuthManager } from '@/utils/auth';
import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { useApiServerHealthCheck } from '@/utils/hooks/useApiServerHealthCheck';
import { useBrowserData } from '@/utils/hooks/useBrowserData';
import { useRouterReady } from '@/utils/hooks/useRouterIsReady';
import { useLanguageManager } from '@/utils/language';
import { useLocalStorageManager } from '@/utils/localstorage';
import { debugLog } from '@/utils/logger';
import { useNotificationManager } from '@/utils/notification';
import normalizeQueryValue from '@/utils/route';
import { useKoreanEulaModal } from '@/utils/shared-state/useKoreanEulaModal';
import { useShowUserEula } from '@/utils/shared-state/useShowUserEula';
import { isBrowserStandaloneMode, safeJsonParse } from '@pi-xcels/utils';
import { computed, ref, unref, watch, watchEffect } from 'vue';
import { useRoute, useRouter } from 'vue-router';

const route = useRoute();

const router = useRouter();
const alertState = useAlertStore();
const uiState = useUiStore();
const featureState = useFeatureStore();
const userState = useUserStore();

const isProductionMode = computed(() => !(featureState.sandboxMode || featureState.demoMode));
const pwaInstallPrompt = ref(false);
const { showUserEula } = useShowUserEula();
const { koreanEulaModal } = useKoreanEulaModal();

const authManager = useAuthManager();
const cookieManager = useCookieManager();
const languageManager = useLanguageManager();
const localStorageManager = useLocalStorageManager();
const NotificationManager = useNotificationManager();
const { isIos } = useBrowserData();
useApiServerHealthCheck();

const { setupAuthOnAppCreated, isAuthenticated, isAnonymous, isAuthenticating, piperksLogout, signKoreanEula, userDataIsLoaded } = authManager;

const closeLoginDialog = () => {
  uiState.showLoginDialog = false;
};

const toggleUserEula = () => {
  showUserEula.value = !showUserEula.value;
};

//Disagreeing kr EULA should restrict users from using our app.
const onCancelKREula = () => {
  koreanEulaModal.value = false;

  cookieManager.removeTokenCookie();
  localStorageManager.removeBaseCountry();
  languageManager.setAppLanguage('en-US');

  if (route.path === '/receipt') {
    uiState.showLoginDialog = true;
  } else {
    router.push({ path: '/auth/login' });
  }
};

const addToHomeScreen = () => {
  // Hide the app provided install promotion
  if ((window as any).deferredPrompt) {
    // Show the install prompt
    (window as any).deferredPrompt.prompt();
    // Wait for the user to respond to the prompt
    (window as any).deferredPrompt.userChoice.then((choiceResult: any) => {
      if (choiceResult.outcome === 'accepted') {
        debugLog('User accepted the install prompt');
      } else {
        debugLog('User dismissed the install prompt');
      }
    });

    closePWAPrompt();
  }
};

const closePWAPrompt = () => {
  pwaInstallPrompt.value = false;
};

// mock deferredPrompt for testing
if (ENV.NX_ENVIRONMENT === 'testing') {
  (window as any).deferredPrompt = {
    prompt: () => {},
    userChoice: new Promise((resolve) => {
      resolve({ outcome: 'accepted' });
    }),
  };
}
// Change the PWA installation prompt status
watchEffect(() => {
  if ((window as any).deferredPrompt && userState.pwa && !isBrowserStandaloneMode() && !safeJsonParse(localStorageManager.PWAInstalled.value) && !isIos.value) {
    pwaInstallPrompt.value = true;
  }
});

// Change the push notification status
watchEffect(() => {
  uiState.shouldAskNotificationPerm = NotificationManager.shouldAskNotificationPerm();

  NotificationManager.checkPushNotificationSupport();
});

const { mutateAsync: userUpdate } = useUpdateUserInfo();

// Change the app language based on user chosen language
// or other fallback values (en) if its not exist
watchEffect(() => {
  if (!unref(userDataIsLoaded)) return;
  const lang = languageManager.convertFormat(
    userState.language?.substring(0, 2) || localStorageManager.language.value?.substring(0, 2) || (navigator.language ?? '')?.substring(0, 2) || 'en'
  );
  languageManager.setAppLanguage(lang);
  if (!isAuthenticated.value) return;
  if (userState.language === lang) return;
  userState.language = lang;
  userUpdate({
    variables: {
      data: {
        language: lang,
      },
    },
  });
});

// Set the base country depending on the query location param
watchEffect(() => {
  if (route?.query?.location) {
    localStorageManager.setBaseCountry(normalizeQueryValue(route.query.location));
  }
});

// Route guard to auto redirect user based on
// their authentication status
watch([route, isAnonymous, koreanEulaModal], () => {
  const publicPages = ['/auth/login', '/receipt'];
  const authRequired = !publicPages.includes(route.path);

  if (authRequired && isAnonymous.value && !koreanEulaModal.value) {
    piperksLogout();
  }
});

const { isRouterNotReady, isRouterReady } = useRouterReady();
watch(
  isRouterReady,
  (isRouterReady) => {
    if (isRouterReady) {
      // Handle all kinds of logins for user
      setupAuthOnAppCreated();
    }
  },
  { immediate: true }
);

// Set app language to current language from local storage if any
watch(
  languageManager.availableLanguages,
  () => {
    const currentLanguage = localStorageManager.language.value;
    if (languageManager.availableLanguages.value.includes(currentLanguage)) {
      languageManager.setAppLanguage(currentLanguage);
    }
  },
  { immediate: true }
);
</script>
