import { useUserLogin } from '@/api/user';
import { useCookieManager } from '@/utils/cookie';
import { SocialLogin } from '@pi-xcels/utils';
import { useMutation } from '@tanstack/vue-query';
import { onAuthStateChanged } from 'firebase/auth';
import { auth as firebaseAuth } from '../../firebase';

export function useFirebaseAuthCallback(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const CookieManager = useCookieManager();

  const onSuccess = async (accessToken: string) => {
    const bearerTokenForLogin = CookieManager.encodeToken({ token: accessToken, provider: SocialLogin.Firebase });
    await piperksLogin({
      httpHeaders: {
        authorization: `Bearer ${bearerTokenForLogin}`,
      },
    });
  };
  const mutation = useHandleFirebaseAuthCallback({
    onSuccess,
  });
  return { handleFirebaseAuthCallback: mutation };
}

function handleFirebaseAuthCallback() {
  return new Promise((resolve, reject) => {
    onAuthStateChanged(firebaseAuth, (user: any) => {
      user
        .getIdToken()
        .then((idToken: any) => {
          resolve(idToken);
        })
        .catch((error: any) => {
          reject(error);
        });
    });
  });
}

function useHandleFirebaseAuthCallback({ onSuccess }: { onSuccess: (d: any) => any }) {
  return useMutation({
    mutationFn: handleFirebaseAuthCallback,
    onSuccess,
  });
}
