import { ref } from 'vue';
import { defineStore } from 'pinia';

export const useUserStore = defineStore('user', () => {
  const id = ref('');
  const name = ref('');
  const phoneNo = ref('');
  const email = ref('');
  const accountType = ref('');
  const dob = ref('');
  const gender = ref('');
  const picture = ref('');
  const address = ref('');
  const baseCountry = ref('');
  const language = ref('');
  const pushNotificationEnabled = ref(false);
  const pwa = ref('');
  const geoLocationEnabled = ref(false);
  const countrySupport = ref({});
  const yob = ref('');
  const showDobGender = ref(false);

  const setData = (data: any) => {
    id.value = data.id;
    name.value = data.name;
    phoneNo.value = data.phoneNo;
    email.value = data.email;
    accountType.value = data.accountType;
    dob.value = data.dob;
    gender.value = data.gender;
    picture.value = data.picture;
    address.value = data.address;
    baseCountry.value = data.baseCountry;
    language.value = data.language;
    pwa.value = data.pwa;
    pushNotificationEnabled.value = data.pushNotificationEnabled;
    geoLocationEnabled.value = data.geoLocationEnabled;
    countrySupport.value = data.countrySupport;
    yob.value = data.yob;
    showDobGender.value = data.showDobGender;
  };

  const $reset = () => {
    id.value = '';
    name.value = '';
    phoneNo.value = '';
    email.value = '';
    accountType.value = '';
    dob.value = '';
    gender.value = '';
    picture.value = '';
    address.value = '';
    pwa.value = '';
    baseCountry.value = '';
    language.value = '';
    pushNotificationEnabled.value = false;
    geoLocationEnabled.value = false;
    countrySupport.value = {};
    yob.value = '';
    showDobGender.value = false;
  };

  return {
    id,
    name,
    phoneNo,
    email,
    accountType,
    dob,
    gender,
    picture,
    address,
    pwa,
    baseCountry,
    language,
    pushNotificationEnabled,
    geoLocationEnabled,
    countrySupport,
    yob,
    showDobGender,

    setData,
    $reset,
  };
});
