import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { readonly, ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';
import { useGetServerHealth } from '../../api/health';
import { useFullScreenErrorMsg } from './useErrorHandling';

export const useApiServerHealthCheck = () => {
  const { error, refetch } = useGetServerHealth();
  const { t } = useI18n();
  const route = useRoute();

  watch(route, () => {
    refetch();
  });

  useFullScreenErrorMsg(
    error,
    readonly(
      ref({
        id: 'server-unavailable',
        title: t('Error.ServerUnavailable.Title'),
        body: t('Error.ServerUnavailable.Body'),
        btnTxt: t('Error.BtnTextRetry'),
        btnAction: () => {
          window.location.reload();
        },
        icon: errorIcon,
        priority: 1,
      })
    )
  );
};
