import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { createRouter, createWebHistory } from 'vue-router';

const router = createRouter({
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { top: 0 };
    }
  },
  history: createWebHistory(ENV.BASE_URL),
  routes: [
    {
      path: '/',
      redirect: (to) => {
        // Backward compatibility with old apps that has '#' in url
        // e.g : https://piperks.com/#/app/receipt
        if (to.hash) {
          // Redirect to url without hash automatically with query param if exist
          const queryString = to.hash.split('?')[1];
          const query: Record<string, string> = {};
          if (queryString) {
            const param = new URLSearchParams(queryString);
            param.forEach((value, key) => {
              query[key] = value;
            });
          }
          return { path: '/' + to.hash.substring(2), query, hash: '' };
        } else {
          return { path: '/app/receipts' };
        }
      },
      meta: {
        authRequired: true,
      },
    },
    {
      path: '/app',
      children: [
        {
          path: 'receipts',
          component: () => import('../views/Receipt/Receipts.vue'),
          meta: {
            authRequired: true,
          },
        },
        {
          path: 'account',
          component: () => import('../views/Account/Account.vue'),
          meta: {
            authRequired: true,
          },
        },
      ],
    },
    {
      path: '/auth',
      redirect: () => {
        return { path: '/auth/login' };
      },
      children: [
        {
          name: 'select-country',
          path: 'login',
          component: () => import('../views/Login/SelectCountry.vue'),
          meta: {
            authRequired: false,
          },
        },
      ],
    },
    {
      name: 'login',
      path: '/auth/login/:country',
      props: true,
      component: () => import('../views/Login/Login.vue'),
      meta: {
        authRequired: false,
      },
    },
    {
      path: '/auth/callback/:provider', // Use a route parameter to capture the provider
      name: 'auth-callback',
      meta: {
        authRequired: false,
      },
      component: () => import('../views/Login/AuthCallback.vue'),
      props: true,
    },
    {
      path: '/receipt',
      component: () => import('../views/WaitingRoom/WaitingRoom.vue'),
      meta: {
        authRequired: false,
      },
    },
    // 404
    {
      path: '/:pathMatch(.*)*',
      redirect: () => {
        return { path: '/' };
      },
      meta: {
        authRequired: false,
      },
    },
  ],
});

router.beforeEach((to, _from, next) => {
  const CookieManager = useCookieManager();
  const routeTarget = to.path;
  const authRequired = to.meta.authRequired;
  const maybeAuthenticated = CookieManager.getTokenCookie();

  if (authRequired && !maybeAuthenticated) {
    next('/auth/login');
    return;
  }
  // Redirect the naver to use query instead of hash
  if (to.name === 'auth-callback' && to.params.provider === 'naver' && to.hash.includes('access_token')) {
    const newPath = `${to.path}?${to.hash.substring(1)}`;
    next(newPath);
    return;
  }

  // Accessing login / select country page when authenticated should forward user to '/' route
  if ((to.name === 'login' || to.name === 'select-country') && maybeAuthenticated && !to.query?.code && !to.query?.access_token) {
    next('/');
    return;
  }

  if (routeTarget === '/receipt' && !to.query?.v) {
    next('/');
    return;
  }

  if (to.hash && to.hash.substr(0, 2) === '/#') {
    next(routeTarget);
  } else {
    next();
  }
});

export default router;
