import { useUserLogin } from '@/api/user';
import { useCookieManager } from '@/utils/cookie';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { useMutation } from '@tanstack/vue-query';

export function useRefreshToken(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const CookieManager = useCookieManager();

  function refreshToken() {
    return piperksLogin({
      httpHeaders: {
        authorization: `Bearer ${CookieManager.getTokenCookie()}`,
      },
    });
  }

  return useMutation({
    mutationKey: [MutationKeys.RefreshToken],
    mutationFn: refreshToken,
  });
}
