import { ref } from 'vue';
import { defineStore } from 'pinia';

export const useUiStore = defineStore('ui', () => {
  const showLoginDialog = ref(false);
  const otpSent = ref(false);
  const showEula = ref(false);
  const shouldAskNotificationPerm = ref(false);
  const googleLoginLoading = ref(true);
  const googleLoginButton = ref<any>(null);

  const language = ref('en');

  return {
    showLoginDialog,
    otpSent,
    showEula,
    shouldAskNotificationPerm,
    language,
    googleLoginLoading,
    googleLoginButton,
  };
});
