import { bool, cleanEnv, makeValidator, num, str } from 'envalid';

export enum NodeEnvironment {
  Development = 'development',
  Production = 'production',
  Test = 'testing',
  Sandbox = 'sandbox',
  Staging = 'staging',
}

const isValidUrl = (input: string) => {
  try {
    new URL(input);
  } catch (error) {
    return false;
  }
  return true;
};

const graphqlUrl = makeValidator((input: string) => {
  if (!isValidUrl(input)) throw new Error('NX_API_URL must be a valid URL');
  if (!input.endsWith('/graphql')) {
    throw new Error('NX_API_URL must end with `/graphql`. e.g http://localhost:5000/graphql and not http://localhost:5000/');
  }
  return input;
});

const rootUrl = makeValidator((input: string) => {
  if (!isValidUrl(input)) throw new Error('NX_ROOT_URL must be a valid URL');
  if (input.endsWith('/')) {
    throw new Error('NX_ROOT_URL must not end with `/`. e.g http://localhost:3000 and not http://localhost:3000/');
  }
  return input;
});

export const validateEnv = (env: Record<string, any>) => {
  const isDevEnv = env.NX_ENVIRONMENT === NodeEnvironment.Development || !!env.DEV;
  const isTestEnv = env.NX_ENVIRONMENT === NodeEnvironment.Test;
  return cleanEnv(
    env,
    {
      NX_ENVIRONMENT: str({ choices: Object.values(NodeEnvironment) }),
      NX_GOOGLE_CLIENT_ID: str(),
      NX_FACEBOOK_APP_ID: str(),
      NX_FACEBOOK_APP_VERSION: str(),
      NX_KAKAO_CLIENT_ID: str(),
      NX_KAKAO_REST_API_KEY: str(),
      NX_NAVER_CLIENT_ID: str(),
      NX_NAVER_CLIENT_SECRET: str(),
      NX_LINE_CLIENT_ID: str(),
      NX_ROOT_URL: rootUrl(),
      NX_DOMAIN: str(),
      NX_MEASUREMENT_ID: str(),
      NX_PUBLIC_VAPID_KEY: str(),
      NX_FIREBASE_API_KEY: str(),
      NX_AUTH_DOMAIN: str(),
      NX_DATABASE_URL: str(),
      NX_PROJECT_ID: str(),
      NX_STORAGE_BUCKET: str(),
      NX_MESSAGING_SENDER_ID: str(),
      NX_APP_ID: str(),
      NX_API_URL: graphqlUrl(),
      NX_SANDBOX_MODE: bool(),
      NX_NAME: str(),
      NX_FORCE_DEMO_MODE: bool({ default: false }),
      NX_SENTRY_DSN: str({
        // NX_SENTRY_DSN required if NX_ENVIRONMENT is not development or test
        ...((isDevEnv || isTestEnv) && { default: undefined }),
      }),
      NX_SENTRY_ORGANIZATION: str({ default: 'pi-xcels' }),
      NX_SENTRY_PROJECT: str({ default: 'web' }),
      SENTRY_AUTH_TOKEN: str({ default: undefined }),
      NX_SENTRY_TRACE_SAMPLE_RATE: num({ default: undefined }),
      NX_SENTRY_SESSION_SAMPLE_RATE: num({ default: undefined }),
      NX_SENTRY_ERROR_SAMPLE_RATE: num({ default: undefined }),
      NX_GTM_CONTAINER_ID: str(),
      NX_GTM_AUTH: str(),
      NX_GTM_PREVIEW: str(),
      NX_GTM_COOKIES: str(),
      NX_JWT_PROVIDER: str({ default: 'piperks' }),
      NX_TASK_TARGET_PROJECT: str({ default: '' }),
    },
    {
      ...(typeof window !== 'undefined' && {
        reporter: ({ errors }) => {
          if (typeof errors === 'object' && !Object.entries(errors).length) return;
          // skip validation in cypress
          if (import.meta.env.BASE_URL.includes('/__cypress/src/')) return;
          throw new Error(`Invalid environment variables: ${JSON.stringify(errors, null, 2)}`);
        },
      }),
    }
  );
};
