import { getBrowserData } from '@pi-xcels/utils';
import { computed, onMounted, ref } from 'vue';
import { debugLog } from '../logger';

export const useBrowserData = () => {
  const browserData = ref<Awaited<ReturnType<typeof getBrowserData>>>({});

  const isIos = computed(() => browserData.value.os?.name === 'iOS');

  onMounted(async () => {
    await getBrowserData()
      .then((deviceData) => {
        browserData.value = deviceData;
      })
      .catch((err) => {
        debugLog(err);
      });
  });

  return {
    browserData,
    isIos,
  };
};
