import { useCookieManager } from '@/utils/cookie';
import { useRoute, useRouter } from 'vue-router';

export function useClearSessionAndRedirect() {
  const router = useRouter();
  const route = useRoute();
  const CookieManager = useCookieManager();
  return {
    clearSessionAndRedirect() {
      CookieManager.removeTokenCookie();
      if (route.path === '/auth/login') router.replace({ query: {} });
      else router.push({ path: '/auth/login' });
    },
  };
}
