import { useAddUserDevice, useUpdateUser, useUpdateUserInfo } from '@/api/user';
import { ENV } from '@/utils/env';
import normalizeQueryValue from '@/utils/route';
import { MutationKeys } from '@pi-xcels/graphql/constants';
import { UserMutation } from '@pi-xcels/graphql/types';
import errorIcon from '@pi-xcels/ui-components/assets/images/error.svg';
import { GtmEvents, KakaoAuth, NaverAuth, SocialLogin, getBrowserData, pushToDataLayer } from '@pi-xcels/utils';
import * as Sentry from '@sentry/vue';
import { useMutation } from '@tanstack/vue-query';
import once from 'just-once';
import pWaitFor from 'p-wait-for';
import { Ref, computed, readonly, ref, unref, watch } from 'vue';
import { useI18n } from 'vue-i18n';
import { RouteLocationNormalizedLoaded, useRoute, useRouter } from 'vue-router';
import { useFeatureStore } from '../stores/feature';
import { useUiStore } from '../stores/ui';
import { useUserStore } from '../stores/user';
import { useAnonymousLogin } from './auth/useAnonymousLogin';
import { useClearSessionAndRedirect } from './auth/useClearSessionAndRedirect';
import { useFacebookAuthCallback, useFacebookLogin } from './auth/useFacebookLogin';
import { useFirebaseAuthCallback } from './auth/useFirebaseLogin';
import { useGoogleAuthCallback } from './auth/useGoogleLogin';
import { useKakaoLogin } from './auth/useKakaoLogin';
import { useLineAuth, useLineAuthCallback } from './auth/useLineLogin';
import { useLogout } from './auth/useLogout';
import { useNaverAuthCallback } from './auth/useNaverLogin';
import { usePiperksLogin } from './auth/usePiperksLogin';
import { useRefreshToken } from './auth/useRefreshToken';
import { useCookieManager } from './cookie';
import { useFullScreenErrorMsg } from './hooks/useErrorHandling';
import { useLanguageManager } from './language';
import { useLocalStorageManager } from './localstorage';
import { debugLog } from './logger';
import { useKoreanEulaModal } from './shared-state/useKoreanEulaModal';
import { useShowUserEula } from './shared-state/useShowUserEula';

interface GtmEvent {
  event: string;
  provider: string;
}
interface GtmLoginEvents {
  [key: string]: GtmEvent;
}

/**
 * use `once` to prevent multiple instances of the same hook
 */
export const useAuthManager = once(() => {
  const route = useRoute();
  const router = useRouter();
  const CookieManager = useCookieManager();
  const LocalStorageManager = useLocalStorageManager();
  const { loginProvider, provider, tokenRef } = CookieManager;
  const { koreanEulaModal } = useKoreanEulaModal();
  const featureState = useFeatureStore();

  const { piperksLogout } = useLogout({
    loginProvider,
    onDone: () => {},
  });
  const { clearSessionAndRedirect } = useClearSessionAndRedirect();
  const { mutateAsync: updateUser } = useUpdateUser();
  const { mutateAsync: getUserData, isSuccess: isGetUserDataSuccess, data: userData } = useGetUserData({ updateUser });
  const { piperksLogin, isPiperksLoginError, isPiperksLoginSuccess, isPiperksLoginLoading, piperksLoginData } = usePiperksLogin();
  const { mutateAsync: getUserDataCallback, data: getUserDataCallbackData } = useGetUserDataCallback({ piperksLogout });

  const { facebookLogin } = useFacebookLogin();
  const { handleFacebookAuthCallback } = useFacebookAuthCallback(piperksLogin);
  const { handleFirebaseAuthCallback } = useFirebaseAuthCallback(piperksLogin);
  const { handleGoogleAuthCallback, googleAuth } = useGoogleAuthCallback(piperksLogin);
  const { handleNaverAuthCallback, naverLoginButton } = useNaverAuthCallback(piperksLogin);
  const { handleKakaoAuthCallback, isLoading: isKakaoLoginLoading } = useKakaoLogin(piperksLogin);
  const { handleLineAuthCallback, isLoading: isLineLoginLoading } = useLineAuthCallback(piperksLogin);
  const { mutateAsync: anonymousLogin, isLoading: isLoggingInAsAnonymous } = useAnonymousLogin(piperksLogin);
  const { mutateAsync: refreshToken, isLoading: isRefreshingToken } = useRefreshToken(piperksLogin);
  const { lineAuth } = useLineAuth();

  const isAuthenticating = computed(() => {
    const isLoggingInAsNormalUser = unref(isPiperksLoginLoading) && !unref(isLoggingInAsAnonymous) && !unref(isRefreshingToken);
    return (
      isLoggingInAsNormalUser ||
      unref(isKakaoLoginLoading) ||
      unref(handleNaverAuthCallback.isLoading) ||
      unref(handleFacebookAuthCallback.isLoading) ||
      unref(handleFirebaseAuthCallback.isLoading) ||
      unref(facebookLogin.isLoading) ||
      unref(isLineLoginLoading)
    );
  });

  const isAnonymous = computed(() => provider.value === 'anonymous');
  const isAuthenticated = computed(() => !unref(isAuthenticating) && unref(provider) === ENV.NX_JWT_PROVIDER);
  const userDataIsLoaded = computed(() => !!getUserDataCallbackData.value);

  const socialLogins = Object.values(SocialLogin);

  watch(isAuthenticated, (newIsAuthenticatedValue) => {
    // Redirect user to homepage if user is authenticated and still on login page
    // Or redirect back to receipt page after logging in via 3rd party auth
    if (newIsAuthenticatedValue && (route.name === 'login' || route.name === 'select-country')) {
      if (LocalStorageManager.hashedReceipt.value) {
        return router.push({ path: '/receipt', query: { v: LocalStorageManager.hashedReceipt.value } });
      }
      return router.push('/');
    }
  });

  watch([isGetUserDataSuccess, userData, computed(() => featureState.demoModeIsLoaded)], ([isGetUserDataSuccess, userData, demoModeIsLoaded]) => {
    if (!isGetUserDataSuccess) return;
    if (!userData) return;
    // getUserDataCallback is using featureState.demoMode, so we need to wait for it to be ready
    if (!demoModeIsLoaded) return;
    getUserDataCallback({ token: unref(tokenRef)!, isAnonymous, data: userData });
  });

  watch([isPiperksLoginSuccess, piperksLoginData], ([isPiperksLoginSuccess, piperksLoginData]) => {
    if (!isPiperksLoginSuccess) return;
    if (!piperksLoginData?.login) {
      debugLog('login data is undefined', piperksLoginData);
      return;
    }
    const { token, authProvider, loginProvider } = piperksLoginData.login;
    if (!authProvider) {
      debugLog('WARN: authProvider is undefined, please check the auth logic or the event tracking', authProvider);
      Sentry.captureMessage(`authProvider is undefined`);
    }

    // Track event for successful initial sign-in using any provider
    handleAfterAuthEventTracking(loginProvider, route);

    CookieManager.setTokenCookie(token);

    getUserData();
  });

  watch(isPiperksLoginError, (isPiperksLoginError) => {
    if (!isPiperksLoginError) return;
    clearSessionAndRedirect();
  });

  function signKoreanEula() {
    koreanEulaModal.value = false;
    CookieManager.updateTokenCookie({ signEula: true });
    piperksLogin({
      httpHeaders: {
        ...(isAnonymous.value && {
          ['x-pixcels-session']: 'anonymous',
        }),
      },
    });
  }

  function handleLineLoginClick() {
    LocalStorageManager.setHashedReceipt(normalizeQueryValue(route.query?.v));
    lineAuth.value?.userLogin();
  }

  async function handleNaverLoginClick() {
    await pWaitFor(() => !!naverLoginButton.value?.el?.childElementCount);
    LocalStorageManager.setHashedReceipt(normalizeQueryValue(route.query?.v));
    NaverAuth.userLogin(naverLoginButton.value?.el);
  }

  async function handleKakaoLoginClick() {
    await pWaitFor(() => !!(window as any)?.Kakao?.Auth?.authorize);
    LocalStorageManager.setHashedReceipt(normalizeQueryValue(route.query?.v));
    KakaoAuth.userLogin(`${ENV.NX_ROOT_URL}/auth/callback/kakao`);
  }

  async function setupAuthOnAppCreated(): Promise<void> {
    handleGoogleAuthCallback();

    if (unref(provider) === ENV.NX_JWT_PROVIDER) {
      refreshToken();
    } else {
      if (route.path === '/receipt')
        // Login as anonymous user when they open a receipt in unauthenticated state
        anonymousLogin();
    }
  }

  return {
    socialLogins,
    signKoreanEula,
    piperksLogout,
    handleNaverLoginClick,
    handleKakaoLoginClick,
    handleLineLoginClick,
    setupAuthOnAppCreated,
    handleKakaoAuthCallback,
    handleNaverAuthCallback,
    handleLineAuthCallback,
    isAuthenticated,
    tokenRef,
    isAnonymous,
    isAuthenticating,
    isRefreshingToken,
    isLoggingInAsAnonymous,
    googleAuth,
    facebookLogin,
    handleFirebaseAuthCallback,
    handleFacebookAuthCallback,
    userDataIsLoaded,
  };
});

function useGetUserDataCallback({ piperksLogout }: { piperksLogout: ReturnType<typeof useAuthManager>['piperksLogout'] }) {
  const featureState = useFeatureStore();
  const LanguageManager = useLanguageManager();
  const LocalStorageManager = useLocalStorageManager();
  const { koreanEulaModal } = useKoreanEulaModal();
  const { showUserEula } = useShowUserEula();
  const userState = useUserStore();
  const { mutateAsync: userUpdate } = useUpdateUserInfo();

  async function getUserDataCallback({ token, isAnonymous, data }: { token: string; isAnonymous: Ref<boolean>; data: UserMutation }): Promise<boolean> {
    if (unref(isAnonymous) || !data.user) {
      debugLog('ANONYMOUS SESSION ACTIVE', token);
      return true;
    }

    debugLog('USER SESSION ACTIVE', token);

    const currentUserData = data?.user;
    if (!currentUserData.id || !token) {
      piperksLogout();
      return false;
    }

    // If user is expired while the app is in demo mode
    if (currentUserData.isDemoExpired && featureState.demoMode) {
      debugLog('DEMO ACCOUNT USER IS ALREADY EXPIRED');

      piperksLogout();
      return false;
    }

    // Show korean user eula if needed
    if (currentUserData?.showEula) {
      if (currentUserData?.baseCountry === 'kr') {
        LanguageManager.setAppLanguage('ko-KR');
        koreanEulaModal.value = true;
      } else if (Array.isArray(currentUserData?.usersEula) && currentUserData?.usersEula.length === 0) {
        showUserEula.value = true;
      }
    }

    // Set new user auto select country
    if (!currentUserData?.baseCountry && LocalStorageManager.basecountry.value) {
      await userUpdate({
        variables: {
          data: {
            baseCountry: LocalStorageManager.basecountry.value,
          },
        },
      });
    }

    userState.setData(currentUserData);
    return true;
  }

  return useMutation({
    mutationKey: [MutationKeys.GetUserDataCallback],
    mutationFn: getUserDataCallback,
  });
}

function useGetUserData({ updateUser }: { updateUser: ReturnType<typeof useUpdateUser>['mutateAsync'] }) {
  const route = useRoute();
  const uiState = useUiStore();
  const { t } = useI18n();
  const { mutateAsync: userDevice } = useAddUserDevice();
  const getUserData = async () => {
    uiState.showLoginDialog = false;
    const transactionRef = normalizeQueryValue(route.query?.v);
    const transactionMode = normalizeQueryValue(route.query?.mode);
    const [, userData] = await Promise.all([
      getBrowserData()
        .then((deviceData) => userDevice({ variables: { data: { device: JSON.stringify(deviceData) } } }))
        .catch((err) => {
          console.error(err);
          Sentry.captureException(err);
        }),
      updateUser({
        variables: {
          data: {
            transactionRef,
            transactionMode,
          },
          where: {
            transaction: { ref: transactionRef },
          },
        },
      }),
    ]);
    return userData;
  };
  const mutation = useMutation({
    mutationKey: [MutationKeys.GetUserData],
    mutationFn: getUserData,
  });
  const { error } = mutation;
  useFullScreenErrorMsg(
    error,
    readonly(
      ref({
        id: 'get-user-data-error',
        title: t('Error.AccountRetrievalFailed.Title'),
        body: t('Error.AccountRetrievalFailed.Body'),
        btnTxt: t('Error.BtnTextRetry'),
        btnAction() {
          window.location.reload();
        },
        icon: errorIcon,
      })
    )
  );
  return mutation;
}

// Track event for successful initial sign-in using any provider
function handleAfterAuthEventTracking(loginProvider: any, route: RouteLocationNormalizedLoaded) {
  if (!!loginProvider && loginProvider !== ENV.NX_JWT_PROVIDER && loginProvider !== 'anonymous') {
    const loginEvent = (GtmEvents.LoginSuccess as GtmLoginEvents)[loginProvider.toLowerCase()];
    if (loginEvent) {
      pushToDataLayer(loginEvent);
    } else {
      debugLog('WARN: given provider is not available on tracking event list, please check the auth logic or the event tracking', loginProvider);
      Sentry.captureMessage(`${loginProvider} is not available on or tracking event list`);
    }
    // Track event for successful saving receipt from anon user to signed-in user (user signing in on the receipt page)
    if (route.path === '/receipt') {
      pushToDataLayer(GtmEvents.SaveReceipt.AnonymousToSignedIn);
    }
  }
}
