import { MutationKeys } from '@pi-xcels/graphql/constants';
import { useMutation } from '@tanstack/vue-query';
import { RecaptchaVerifier, signInWithPhoneNumber, signOut } from 'firebase/auth';
import { auth } from '.';

function renderRecaptcha(payload: any) {
  (window as any).recaptchaVerifier = new RecaptchaVerifier(auth, payload.recaptchaContainer, {
    size: 'invisible',
  });
}

async function firebasePhoneAuth(payload: any) {
  renderRecaptcha(payload);
  try {
    const confirmationResult = await signInWithPhoneNumber(auth, payload.phoneNumber, (window as any).recaptchaVerifier);

    // SMS sent. Prompt user to type the code from the message, then sign the
    // user in with confirmationResult.confirm(code).
    (window as any).confirmationResult = confirmationResult;
    return true;
  } catch (error) {
    (window as any).recaptchaVerifier.clear();
    throw error;
  }
}

function useFirebasePhoneAuth() {
  return useMutation({
    mutationKey: [MutationKeys.FirebasePhoneAuth],
    mutationFn(payload: { phoneNumber: string; recaptchaContainer: string }) {
      return firebasePhoneAuth(payload);
    },
  });
}

function useVerifyFirebasePhoneAuth() {
  return useMutation({
    mutationKey: [MutationKeys.VerifyFirebasePhoneAuth],
    mutationFn(payload: any) {
      return (window as any).confirmationResult.confirm(payload.otpCode);
    },
  });
}

async function logout() {
  try {
    await signOut(auth);
    return { success: true, message: 'Logged out successfully' };
  } catch (error) {
    return { success: true, message: error };
  }
}

export { logout, renderRecaptcha, useFirebasePhoneAuth, useVerifyFirebasePhoneAuth };
