import { useUserLogin } from '@/api/user';
import { useAlertStore } from '@/stores/alert';
import { useUiStore } from '@/stores/ui';
import { useCookieManager } from '@/utils/cookie';
import { ENV } from '@/utils/env';
import { useModalErrorMsg } from '@/utils/hooks/useErrorHandling';
import { NaverAuth, SocialLogin } from '@pi-xcels/utils';
import { useMutation } from '@tanstack/vue-query';
import once from 'just-once';
import { readonly, ref, render, watchEffect } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from 'vue-router';
import { useLoginMethods } from '../hooks/useLoginMethods';

const naverScript = ref<HTMLScriptElement>();
const naverLoginButton = ref<any>(null);

const naverAuth = ref<NaverAuth>();
const createNaverLoginButton = once(() => naverAuth.value!.createButtonTag());
const renderNaverLoginButton = once(() => render(naverLoginButton.value, document.body));

export function useNaverAuth() {
  return { naverAuth };
}

export function useNaverAuthCallback(piperksLogin: ReturnType<typeof useUserLogin>['mutateAsync']) {
  const { isNaverEnabled } = useLoginMethods();
  const CookieManager = useCookieManager();
  const uiState = useUiStore();
  const route = useRoute();
  const { t } = useI18n();
  const alertState = useAlertStore();

  // Insert Naver SDK
  watchEffect(() => {
    if (!isNaverEnabled.value) return;
    if (!naverAuth.value) naverAuth.value = new NaverAuth(ENV.NX_NAVER_CLIENT_ID, ENV.NX_NAVER_CLIENT_SECRET, uiState);

    // Create naver login button
    if (!naverLoginButton.value) {
      naverLoginButton.value = createNaverLoginButton();
      renderNaverLoginButton();
    }

    // Create naver login script
    if (!naverScript.value) {
      naverScript.value = naverAuth.value.createScriptTag(`${ENV.NX_ROOT_URL}/auth/callback/naver`, naverLoginButton);
      document.head.appendChild(naverScript.value);
    }
  });

  const mutation = useMutation({
    mutationFn: async () => {
      const naverAccessToken = route.query.access_token as string;
      if (!naverAccessToken) throw new Error('naverAccessToken is not defined');
      const bearerTokenForLogin = CookieManager.encodeToken({ token: naverAccessToken, provider: SocialLogin.Naver });
      await piperksLogin({
        httpHeaders: {
          authorization: `Bearer ${bearerTokenForLogin}`,
        },
      });
    },
  });
  const { error } = mutation;
  useModalErrorMsg(
    error,
    readonly(
      ref({
        id: 'naver-login-error',
        title: t('Error.SocialLoginFailed.Title', { provider: 'Naver' }),
        body: t('Error.SocialLoginFailed.Body'),
        btnTxt: t('Error.BtnTextOK'),
        btnAction() {
          alertState.removeAlertMessage('naver-login-error');
        },
      })
    )
  );

  return {
    handleNaverAuthCallback: mutation,
    naverAuth,
    naverScript,
    naverLoginButton,
  };
}
